import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController, Events } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Session, NativeService } from '../../../services/services';
import { AuthProvider, UserProvider } from '../../../providers/providers';

@IonicPage()
@Component({
    selector: 'page-set-password',
    templateUrl: 'set-password.html'
})
export class SetPasswordPage {
    currentUser: any;
    isReadyToSave: boolean;
    isConfirmPass: boolean;
    isCorrect: boolean;
    form: FormGroup;
    passwordStrong: boolean;
    word: string;
    tipsId: string;
    aStr: any;
    tipsIdes: string;
    wordes: string;
    textColr: any;
    reminder: any;

    constructor(public navCtrl: NavController,
        public viewCtrl: ViewController,
        public session: Session,
        private userProvider: UserProvider,
        private authProvider: AuthProvider,
        private nativeService: NativeService,
        private events: Events,
        public formBuilder: FormBuilder,
        private translateService: TranslateService,
        public modalCtrl: ModalController) {
        this.word = ''
        this.tipsId = 'tips';
        this.tipsIdes = 'tips'
        this.isConfirmPass = false;
        this.isCorrect = false;
        this.textColr = "defoult";
        this.reminder = this.translateService.instant('SET_PASSWORD_RULE');
        this.aStr = [
            this.translateService.instant('SET_PASSWORD_COEFFICIENT1'),
            this.translateService.instant('SET_PASSWORD_COEFFICIENT2'),
            '', '', ''
            // this.translateService.instant('SET_PASSWORD_COEFFICIENT3'),
            // this.translateService.instant('SET_PASSWORD_COEFFICIENT4'),
            // this.translateService.instant('SET_PASSWORD_COEFFICIENT5') 
        ]

        if (this.session.user != null) {
            this.currentUser = this.session.user;
        }
        this.form = formBuilder.group({
            newPassword: ['', Validators.required],
            confirmPassword: ['', Validators.required]
        });
        this.isReadyToSave = false;
        // Watch the form for changes, and
        this.form.valueChanges.subscribe((v) => {
            if (this.form.valid) {
                this.veryfy();
            }
        });
    }

    //校验密码复杂系数
    ionViewDidLoad(clack: any) {
        if (this.form.value.newPassword.length < 1) return;
        let num = this.checkStrong(this.form.value.newPassword);

        if (this.form.value.newPassword.length >= 8) {
            switch (num) {
                case 0:
                    this.word = this.aStr[0];
                    this.tipsId = 'tips';
                    break;
                case 1:
                    this.word = this.aStr[1];
                    this.tipsId = 'tips0';
                    break;
                case 2:
                    this.word = this.aStr[2];
                    this.tipsId = 'tips1';
                    break;
                case 3:
                    this.word = this.aStr[2];
                    this.tipsId = 'tips2';
                    break;
                case 4:
                    this.word = this.aStr[2];
                    this.tipsId = 'tips3';
                    break;
            }
            if (num >= 2) {
                this.tipsIdes = 'tips0';
                this.isConfirmPass = true;
                this.textColr = "defoult";
            } else {
                this.textColr = "status";
            }
        } else {
            if (num >= 2) {
                this.textColr = "place";
            } else {
                this.textColr = "invalid";
            }
            this.isConfirmPass = false;
            this.word = "";
            this.tipsId = 'tips';
            this.tipsIdes = 'tips';
            this.wordes = "";
        }
        this.isCorrect = false;
        this.veryfy();

    }
    checkStrong(val: string) {
        let modes = 0;
        if (/\d/.test(val)) modes++;//数字
        if (/[a-z]/.test(val)) modes++;//小写
        if (/[A-Z]/.test(val)) modes++;//大写
        if (/\W/.test(val)) modes++//特殊字符
        return modes
    }
    //校验密码是否一致
    veryfy() {
        if (this.form.value.confirmPassword == this.form.value.newPassword) {
            this.wordes = this.word;
            this.tipsIdes = this.tipsId;
            this.isCorrect = true;
            this.isReadyToSave = true;
        } else {
            this.tipsIdes = 'tips0';
            this.isCorrect = false;
            this.isReadyToSave = false;
        }
    }

    submit() {
        if (!this.form.valid) { return; }
        if (this.form.value.confirmPassword != this.form.value.newPassword) {
            this.nativeService.alert(this.translateService.instant("SET_PASSWORD_CONFIRM_INVLID"));
            return;
        }
        this.authProvider.setPassword({
            password: this.form.value.newPassword.toString()
        }).subscribe((data: any) => {
            this.userProvider.getUserInfo().subscribe((data: any) => {
                this.viewCtrl.dismiss();
            })
        });
    }

}



