    /**
     * 只有一个确定按钮的弹出框.
     * 如果已经打开则不再打开
     */
    alert = (() => {
        let isExist = false;
        return (title: string, subTitle: string = '', message: string = '', callBackFun = null): any => {
            if (!isExist) {
                isExist = true;

                const alert = this.alertCtrl.create({
                    title: title,
                    subTitle: subTitle,
                    message: message,
                    buttons: [{
                        text: this.translateService.instant('APP_COMMON_CONFIRM'),
                        handler: () => {
                            isExist = false;
                            callBackFun && callBackFun();
                        }
                    }],
                    enableBackdropDismiss: false
                });

                alert.present();

            }
        };
    })();

    /**
     * 统一调用此方法显示loading
     * @param content 显示的内容
     */
    showLoading(content: string = ''): void {
        if (!this.loading) {//如果loading已经存在则不再打开
            let loading = this.loadingCtrl.create({
                content: content
            });
            loading.present();
            this.loading = loading;
        }
    };
    /**
   * 关闭loading
   */
    hideLoading(): void {
        this.loading && this.loading.dismiss();
        this.loading = null;
    };

    /**
     * 压缩图片
     * @param path 图片路径
     * @param options MaxWidthOrHeight 压缩图片最大宽度或高度
     */
    compressImage(path: string, options: { targetWidth?: number, targetheight?: number, MaxWidthOrHeight?: number, quality?: number }): Observable<{ result: boolean, dataURL: string }> {
        return new Observable(observe => {
            let img = new Image(); 
            if (this.isIos()) {
                path = path.replace(/^file:\/\//, '');
            }
            img.src = path;
            img.onload = function () {
                // 默认按比例压缩
                let width = options.targetWidth || img.width;
                let height = options. targetheight ||img.height;
                if(!options.MaxWidthOrHeight)options.MaxWidthOrHeight = 1024;
                if(options.MaxWidthOrHeight < width || options.MaxWidthOrHeight < height){
                    let scale = 1;
                    if (width > height) { //宽 > 高
                        scale = width / options.MaxWidthOrHeight;
                    } else { //宽 < 高
                        scale = height / options.MaxWidthOrHeight;
                    }
                    width = width / scale;
                    height = height / scale;
                }
               
                let quality = options.quality || 1;  // 默认图片质量为1
                //生成canvas
                let canvas = document.createElement('canvas');
                let ctx = canvas.getContext('2d');
                // 创建属性节点
                let anw = document.createAttribute("width");
                anw.nodeValue = width.toString();
                let anh = document.createAttribute("height");
                anh.nodeValue = height.toString();
                canvas.setAttributeNode(anw);
                canvas.setAttributeNode(anh);
                ctx.drawImage(img, 0, 0, width, height);
                // 图像质量  
                let base64 = canvas.toDataURL('image/jpeg', quality);
                // 回调函数返回base64的值
                observe.next({ result: true, dataURL: base64 });
            }
        })
    }

    /**
   * 使用cordova-plugin-camera获取照片
   * @param options
   */
    getPicture(options: CameraOptions = {}): Observable<string> {
        let ops: CameraOptions = Object.assign({
            sourceType: this.camera.PictureSourceType.CAMERA,//图片来源,CAMERA:拍照,PHOTOLIBRARY:相册
            destinationType: this.camera.DestinationType.DATA_URL,//默认返回base64字符串,DATA_URL:base64   FILE_URI:图片路径
            quality: QUALITY_SIZE,//图像质量，范围为0 - 100
            allowEdit: true,//选择图片前是否允许编辑
            encodingType: this.camera.EncodingType.JPEG,
            saveToPhotoAlbum: true,//是否保存到相册
            correctOrientation: true//设置摄像机拍摄的图像是否为正确的方向
        }, options);
        return Observable.create(observer => {
            this.camera.getPicture(ops).then((imgData: string) => {
                if (ops.destinationType === this.camera.DestinationType.DATA_URL) {
                    observer.next('data:image/jpg;base64,' + imgData);
                } else {
                    observer.next(imgData);
                }
            }).catch(err => {
                if (err == 20) {
                    this.alert('没有权限,请在设置中开启权限');
                    return;
                }
                if (String(err).indexOf('cancel') != -1) {
                    return;
                }
                this.logger.log(JSON.stringify(err), '使用cordova-plugin-camera获取照片失败');
                //this.alert('获取照片失败');
                observer.error(false);
            });
        });
    };

