    /**
     * 根据图片绝对路径转化为base64字符串
     * @param path 绝对路径
     */
    convertImgToBase64(path: string): Observable<string> {
        return Observable.create(observer => {
            this.file.resolveLocalFilesystemUrl(path).then((fileEnter: FileEntry) => {
                fileEnter.file(file => {
                    let reader = new FileReader();
                    reader.onloadend = function (e) {
                        observer.next(this.result);
                    };
                    reader.readAsDataURL(file);
                });
            }).catch(err => {
                this.logger.log(JSON.stringify(err), '根据图片绝对路径转化为base64字符串失败');
                observer.error(false);
            });
        });
    }
 
    resolveLocalFilesystemUrl(path): Promise<any>  {
        return new Promise((resolve, reject) =>{
            this.file.resolveLocalFilesystemUrl(path).then((fileEnter: FileEntry) => {
                fileEnter.file(file => {
                    resolve(file);
                });
            }).catch(err => {
                reject(err);
            });
        })
    }

