
import { StorageService } from './../services/storage-service';
import { Device } from '@ionic-native/device';

import { ModalFromBottomEnter, ModalFromBottomLeave, ModalFromRightEnter, ModalFromRightLeave, ModalScaleEnter, ModalScaleLeave } from './modal-transitions';

import { IonicApp, IonicModule, Config } from 'ionic-angular';
import { Deeplinks } from '@ionic-native/deeplinks';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { HttpModule } from "@angular/http";
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { Camera } from '@ionic-native/camera';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';

import { AppVersion } from '@ionic-native/app-version';
import { QRScanner } from '@ionic-native/qr-scanner';
import { Clipboard } from "@ionic-native/clipboard";
import { File } from "@ionic-native/file";
import { Sim } from '@ionic-native/sim';
import { FileTransfer } from "@ionic-native/file-transfer";
import { Globalization } from '@ionic-native/globalization';
import { Geolocation } from '@ionic-native/geolocation';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Network } from "@ionic-native/network";
import { AppMinimize } from "@ionic-native/app-minimize"; 
import { Keyboard } from '@ionic-native/keyboard';
import { CallNumber } from "@ionic-native/call-number";
import { ThemeableBrowser } from "@ionic-native/themeable-browser";
import { Diagnostic } from "@ionic-native/diagnostic";
import { FileOpener } from '@ionic-native/file-opener';
import { JPush } from '@jiguang-ionic/jpush'; 
import { UserAgent } from '@ionic-native/user-agent';

import { PipesModule } from './../pipes/pipes.module';
import { IS_DEBUG, FUNDEBUG_API_KEY } from "../services/constants";
import { Logger } from "../services/logger";
import { Session, JPushService, FaceService } from "../services/services";
import { Helper } from "../services/helper";
import { NativeService } from '../services/native-service';
import { HttpService } from '../services/http-service';
import { FileService } from "../services/file-service";
import { FileChooser } from '@ionic-native/file-chooser';
import { CacheProvider, PreloadProvider } from '../providers/providers';
import { UserProvider, AuthProvider } from '../providers/providers';
import { CountryProvider, ConfigProvider, SettingProvider, WidgetProvider, MessageProvider, ServerProvider, ContentProvider, DLRecruitingProvider, IDLRecruitingProvider } from '../providers/providers';

import { MyApp } from './app.component';
import { DragulaModule } from 'ng2-dragula';
import { MomentModule } from 'angular2-moment';
//参考文档:https://docs.fundebug.com/notifier/javascript/framework/ionic2.html
import * as fundebug from "fundebug-javascript";
import { WorkflowProvider } from '../providers/workflow-provider';
import { ContactProvider } from '../providers/contact-provider';
import { ShareProvider } from '../providers/share-provider';
import { BlogProvider } from '../providers/blog-provider';
import { ClubProvider } from '../providers/club-provider';
import { HandleProvider } from '../providers/handle/handle';
import { SqliteProvider } from '../providers/sqlite-provider';
import { SQLite } from '@ionic-native/sqlite';
import { SqliteService } from '../services/sqlite-service'; 
import { PinyinService } from '../services/pinyin';
import { SocialProvider } from '../providers/social/social';

fundebug.apikey = FUNDEBUG_API_KEY;
// fundebug.releasestage = IS_DEBUG ? 'development' : 'production';//应用开发阶段，development:开发;production:生产
//fundebug.silent = !IS_DEBUG;//如果暂时不需要使用Fundebug，将silent属性设为true

export class FunDebugErrorHandler implements ErrorHandler {

  appversion: string;
  isCollectErrorInfo: boolean = false;
  isGetFlag: boolean = false;
  constructor(public device: Device,
    public appVersion: AppVersion,
    public session: Session,
    public handleProvider: HandleProvider,
    public storageService: StorageService) {
       
  }
  async handleError(err: any) {
    console.error(err) 
    if(this.isGetFlag) { 
      this.isCollectErrorInfo && this.sendErrorInfo(err);
    }else{
      this.isCollectErrorInfo = await this.storageService.getItem('isCollectErrorInfo')
      this.isGetFlag = true;
      this.isCollectErrorInfo && this.sendErrorInfo(err);
    } 
  }

  sendErrorInfo(err: any){ 
    this.handleProvider.generate('error', -1, err['message'] ? { message: err['message'], stack: err['stack'] } : err);
    if (this.session && this.session.userId) {
      fundebug.userid = this.session.userId;
    }

    if (this.appversion) {
      fundebug.releasestage = this.appversion;
      fundebug.notifyError(err);
    } else {
      this.appVersion.getVersionNumber().then(currentVersionNo => {
        this.appversion = currentVersionNo;
        err.device = this.device;
        fundebug.releasestage = currentVersionNo;

        fundebug.notifyError(err);
      }).catch(() => {
        fundebug.releasestage = 'PC Simulator';
        fundebug.notifyError(err);
      })
    }
  }
}
// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}
export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any time. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new SettingProvider(storage, {
    optionReceiveNotify: true, // 接收推送通知
    optionLanguage: 'en-US', // 選擇語言,,
    optionShowFabButton: true,
    optionBrowser: false,
    optionDebug: IS_DEBUG,
    optionTheme: false,
    isCollectErrorInfo: false
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {

      tabsPlacement: 'bottom',
      pageTransition: 'md-transition', //The name of the transition to use while changing pages. Available options: "ios-transition", "md-transition", "wp-transition".
      activator: 'ripple',  //Used for buttons, changes the effect of pressing on a button. Available options: "ripple", "highlight".
      tabsHideOnSubPages: 'true',       //隐藏全部子页面tabs
      iconMode: 'ios',                  //引用iOS的返回图标
      mode: 'ios',                      //把平台设置成iOS的风格
      modalEnter: 'modal-slide-in',     //设置返回的动画效果
      modalLeave: 'modal-slide-out',    //设置返回的动画效果
      backButtonText: '',
    }),
    IonicStorageModule.forRoot(),
    PipesModule,
    MomentModule,
    DragulaModule,
    HttpModule,
    HttpClientModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [/*配置不会再模块中使用的组件*/
    MyApp
  ],
  providers: [
    MessageProvider,
    CountryProvider,
    UserProvider,
    AuthProvider,
    WidgetProvider,
    ContentProvider,
    Camera,
    SplashScreen,
    StatusBar,
    AppVersion,
    QRScanner,
    Clipboard,
    NativeService,
    JPush,
    JPushService,
    ConfigProvider,
    HttpService,
    FileService,
    FaceService,
    Network,
    Logger,
    Helper,
    Session,
    File,
    Device,
    FileOpener,
    FileTransfer,
    AppMinimize,
    CallNumber,
    InAppBrowser,
    ThemeableBrowser,
    StorageService,
    Diagnostic,
    Sim,
    Globalization,
    Keyboard,
    Geolocation, 
    { provide: SettingProvider, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: FunDebugErrorHandler, deps: [Device, AppVersion, Session, HandleProvider, StorageService] },
    CacheProvider,
    ServerProvider,
    DLRecruitingProvider,
    IDLRecruitingProvider,
    PreloadProvider,
    WorkflowProvider,
    ContactProvider,
    ShareProvider,
    BlogProvider,
    ClubProvider,
    HandleProvider,
    SqliteProvider,
    SQLite,
    SqliteService, 
    PinyinService,
    Deeplinks,
    SocialProvider,
    UserAgent,
    FileChooser
  ]
})
export class AppModule {
  constructor(public config: Config) {
    this.setCustomTransitions();
  }
  private setCustomTransitions() {
    this.config.setTransition('modal-from-right-enter', ModalFromRightEnter);
    this.config.setTransition('modal-from-right-leave', ModalFromRightLeave);
    this.config.setTransition('modal-from-bottom-enter', ModalFromBottomEnter);
    this.config.setTransition('modal-from-bottom-leave', ModalFromBottomLeave);
    this.config.setTransition('modal-scale-enter', ModalScaleEnter);
    this.config.setTransition('modal-scale-leave', ModalScaleLeave);
  }

}





