import { ClubProvider } from './club-provider';
import { HandleProvider } from './handle/handle';
import { MessageProvider } from './message-provider';
import { ShareMessage } from './share-provider';
import { TranslateService } from '@ngx-translate/core';
import { ModalController } from 'ionic-angular';
import { Logger } from './../services/logger';
import { UserProvider } from './user-provider';
import { Events } from 'ionic-angular';
import { Session } from './../services/session';
import { WidgetProvider } from './widget-provider';
import { Injectable } from '@angular/core'; 
/*
  Generated class for the PreloadProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

export interface ShareModal{
  shareMessage?: ShareMessage,
  navigationDockTargetMapIds?: Array<number>,
  callback?:any,
  qrcode?: string,
  isShareImage:boolean

}

export interface ContactModal{
  title?: string, 
  callback?:any, 
  type: number; //选择后后续动作;1: 发送消息 ,0: 不作任何动作
  params?: any;

}
@Injectable()
export class PreloadProvider {

  constructor(

    public widgetProvider: WidgetProvider,
    public session: Session,
    public events: Events,
    public userProvider: UserProvider,
    public modal: ModalController,
    public logger: Logger,
    public translate: TranslateService,
    public messageProvider: MessageProvider,
    public handleProvider: HandleProvider,
    public clubProvider: ClubProvider) {
    this.events.subscribe('user:permission-changed', () => {
      this.widgetProvider.getAllWidget({ "lang": this.session.language }, true).subscribe((data) => {
        if (data.errcode == 0) {
          this.session.widget = data.result;
        }
      });
    })

    this.events.subscribe('user:info-changed', () => {
      this.widgetProvider.getAllWidget({ "lang": this.session.language }, true).subscribe((data) => {
        if (data.errcode == 0) {
          this.session.widget = data.result;
        }
      });
    })
  }

  IsPunchMember() {
    //预加载是否需要签到
    this.userProvider.IsPunchMember().subscribe((res) => {
      if (res.errcode === 0) {
        this.session.isPunchMember = res.result.isPunchMember;
      }
    });
  }


  getHomeData(isHideLoading) {
    //预加载首页数据
    this.widgetProvider.getHomeData(isHideLoading).subscribe((data: any) => {
      if (data.errcode == 0) {
        this.session.homedata = data.result;
        // this.handleProvider.generate('getHomeData successfully',-1,data.result)
        setTimeout(()=>{
          this.events.publish('user:logined', {
            "userId": this.session.userId,
            "userToken": this.session.token,
            "timestamp": new Date().getTime()
          });
        },1) 
      }
    }, err => {
      this.logger.log(JSON.stringify(err), "getHomeData");
    });
  }

  getCommunityChannel() {
    //预加载首页数据
    this.clubProvider.getCommunityChannel().subscribe((data: any) => {
      this.session.communityColumn = data.result;
    }, err => {
      this.logger.log(JSON.stringify(err), "getCommunityChannel");
    });
  }

  getAllWidget(isHideLoading) {
    //预加载首页数据
    this.widgetProvider.getAllWidget({ "lang": this.session.language }, isHideLoading).subscribe((data) => {
      if (data.errcode == 0) {
        this.session.widget = data.result;
        this.events.publish('previewJobs');
      }
    });
  }

  getUserInfo(isRegister?: Boolean) {
    //预加载个人信息
    this.userProvider.getUserInfo(true, isRegister).subscribe((data: any) => {
      if (data.result.isActive) {
        setTimeout(()=>{
          this.events.publish('user:logined', {
            "userId": this.session.userId,
            "userToken": this.session.token,
            "timestamp": new Date().getTime()
          });
        },1000) 
        
        
      } else {//锁定用户
        this.session.token = '';
        this.session.userId = '';
        this.events.publish('user-isActive:false');
      }
    });
  }

   

  /**
   * 打开Share Modal
   * @param share 
   */
  openShareModal(share: ShareModal){ 
    let md = this.modal.create('SharePage', {
      //數組為空時默認顯示全部類型:1-微信 2-朋友圈 3-微博 4-刷新 5-複製鏈接 6-收藏
      navigationDockTargetMapIds: share.navigationDockTargetMapIds,
      shareMessage:share.shareMessage,
      isShareImage:share.isShareImage,
      callback: share.callback,
      qrcode: share.qrcode
    }, {
      showBackdrop: true,
      enableBackdropDismiss: true,
      enterAnimation: 'modal-from-bottom-enter',
      leaveAnimation: 'modal-from-bottom-leave'
    }); 
    md.present();
    md.onDidDismiss(data => {
      if(share && share.callback && data && data.result){
        share.callback(data)
      } 
    }); 
  }

  openContactChooserPage(contact: ContactModal){ 
    let md = this.modal.create('ContactChooserPage',contact);
    md.present();
    md.onDidDismiss(data => {
      // if(contact.type == 1){
      //   this.messageProvider.sendMessage()
      // }
      if(contact && contact.callback){
        contact.callback(data)
      } 
    }); 
  }
  
  previewImage(imageArray: Array<{pictureUrl: string}>,index: number,callback?: any){ 
    let md = this.modal.create('ImgViewerPage',{
      imageArray: imageArray,
      index: index
    },{ 
      enterAnimation: 'modal-scale-enter',
      leaveAnimation: 'modal-scale-leave',
      cssClass:'img-viewer-backdrop'
    });
 
    md.present().then(res=>{ 
    }).catch(error=>{ 
    }); 
    md.onDidDismiss(data => { 
      callback && callback(data) 
    }); 
  }




}



