import { APPNAME_PAGE_PREFIX } from './../services/constants';
import { Events } from 'ionic-angular';
import { Session } from './../services/session';
import { HttpService } from './../services/http-service';
 
import { Injectable } from '@angular/core'; 
/*
  Generated class for the ContactProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ContactProvider {

  list: any[];
  allItems: any[];
  friendInfo: any;
  friends: any;
  friendTags: any;
  newFriends: any;
  constructor( public session: Session,
    public http: HttpService,
    public events: Events) {  
  }

  getContacts() {
    return this.list;
  }
  
  addContact(item){
    
  }
   

  /**
   * 查找用户
   * @param keyword 账号（手机号码）
   */
  SearchMember(keyword: string){
    let seq = this.http.post('/Friends/SearchMember',true, { 
      lang: this.session.language,
      keyword: keyword
    },true).share() 
    return seq;
  }

  /**
   * 发送添加好友邀请
   * @param FriendId 好友id
   * @param Description 描述
   */
  SendAddRequest(FriendId: string, Description: string){
    let seq = this.http.post('/Friends/SendAddRequest',true, { 
      FriendId: FriendId,
      Description: Description
    }).share() 
    return seq;
  }

  /**
   * 驗證好友請求
   * @param FriendId  好友id  
   * @param Description 描述
   */
  AcceptAddRequest(RequestId: string, IsAccept: boolean){
    let seq = this.http.post('/Friends/AcceptAddRequest',true, { 
      RequestId: RequestId,
      IsAccept: IsAccept
    }).share() 
    return seq;
  }

  /**
   * 获取朋友列表
   */
  MyFriends(){
    let seq = this.http.post('/Friends/MyFriends',true, { 
      lang: this.session.language 
    }).share() 
    seq.subscribe(res=>{
      this.friends = res.result;   
      this.events.publish('contact:refresh-myFriends')
    })
    return seq;
  } 
   

  /**
   * 获取朋友列表
   */
  FriendInfo(FriendID: number){
    let seq = this.http.post('/Friends/FriendInfo',true, { 
      lang: this.session.language ,
      FriendID: FriendID
    }).share() 
    seq.subscribe(res=>{
      this.friendInfo = res.result; 
      this.events.publish('contact: refresh-friendinfo');
    })
    return seq;
  }  

  /**
   * 最近的好友验证
   */
  LatestFriendRequest(){
    let seq = this.http.post('/Friends/LatestFriendRequest',true, { 
      lang: this.session.language 
    },true).share() 
    seq.subscribe(res=>{
      this.newFriends = res.result;
    })
    return seq;
  }  

  /**
   * 移除好友
   * @param FriendId 好友id
   */
  RemoveFriend(FriendId: number){
    let seq = this.http.post('/Friends/RemoveFriend',true, { 
      FriendId: FriendId 
    }).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }  


  /**
   * 好友设置
   * @param FriendId 好友id
   * @param settings 设置
   */
  FriendSetting(FriendId: number, settings: Array<{Name: string, value: any}>){
    let seq = this.http.post('/Friends/FriendSetting',true, { 
      FriendId: FriendId, 
      settings: settings
    }).share() 
    seq.subscribe(res=>{
      this.FriendInfo(FriendId).subscribe()
    })
    return seq;
  }  
  
  /**
   * 黑名单列表 
   */
  BlackList(){
    let seq = this.http.post('/Friends/BlackList',true).share() 
    return seq;
    
  }  

  /**
   * 加入黑名单
   * @param MemberId 用户id
   */
  AddToBlackList(MemberId: number){
    let seq = this.http.post('/Friends/AddToBlackList',true, {MemberId:MemberId}).share() 
    seq.subscribe(res=>{  
    })
    return seq;
  }
  

  /**
   * 移出黑名单
   * @param MemberId 用户id
   */
  RemoveFromBlackList(MemberId: number){
    let seq = this.http.post('/Friends/RemoveFromBlackList',true, {MemberId:MemberId}).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  } 

  generateMemberURL(memberId: string){
   return `${APPNAME_PAGE_PREFIX.toLowerCase()}AppPageId=ContactDetailPage?friendId=${memberId}`;
  }

 
  /**
   * 发送消息
   * Type: 消息類型，Text為文本, Image為圖片(jpeg/png/jpg), Forward為轉發類消息, Card為個人名片, Location為個人位置 
   * 
   * FromType:消息來源, 有四類: 1.Chat 一般聊天,  2.GroupChat 群聊  此時需要傳GroupId,  本接口只有以上這兩種.
      3.Widget 應用,   4.AdminConsole 后臺
   * @param type 
   * @param title 
   * @param content 
   * @param image 
   * @param redirectUrl 
   * @param MemberIds 
   */
  SendMsg( MemberIds: Array<string|number>,messageId: number|string,type:string, title: string, content:string, image?:string,redirectUrl?:string){
    let seq = this.http.post('/Friends/SendMsg',true, {
      "MessageId":messageId,
      "type": type,
      "Lang":this.session.language,
      "fromType":"Chat",
      "title":title,
      "content":content,
      "image": image,
      "redirectUrl":redirectUrl,   
      "ReceiverId": MemberIds,
      "GroupId": 0 
    },true).share() 
    seq.subscribe(res=>{},err=>{})
    return seq;
  } 



  
  FriendTagList( ){
    let seq = this.http.post('/Friends/FriendTagList',true, null).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }
  
  CreateFriendTag(TagName:string, MemberIdList: Array<string|number>){
    let seq = this.http.post('/Friends/CreateFriendTag',true, {
      "TagName":TagName,
      "MemberIdList": MemberIdList
    }).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }


  
  UpdateFriendTag(FriendTagId:string, TagName:string, AddList: Array<string|number>, RemoveList: Array<string|number>){
    let seq = this.http.post('/Friends/UpdateFriendTag',true, {
      "FriendTagId":FriendTagId,
      "TagName":TagName,
      "AddList": AddList,
      "RemoveList": RemoveList
    }).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }
  
  FriendTagMembers(FriendTagId:string ){
    let seq = this.http.post('/Friends/FriendTagMembers',true, {
      "FriendTagId":FriendTagId 
    }).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }

  DeleteFriendTag(FriendTagId:string ){
    let seq = this.http.post('/Friends/DeleteFriendTag',true, {
      "FriendTagId":FriendTagId 
    }).share() 
    seq.subscribe(res=>{ 
    })
    return seq;
  }
   
}



