import { ErrorCode } from './../services/errcode';
import { Logger } from './../services/logger';
import { PreloadProvider } from './preload-provider';
import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';
import { Events, ModalController, LoadingController } from "ionic-angular";
import { TranslateService } from '@ngx-translate/core'; 
import { HttpService } from '../services/http-service';
import { StorageService } from '../services/storage-service';
import { Utility } from '../services/utility';
import { NativeService } from '../services/native-service'; 
import { Session } from '../services/session'; 

import { UserProvider } from './user-provider'; 
@Injectable()
export class AuthProvider {

  constructor(private httpService: HttpService,
    private storageService: StorageService,
    private userProvider: UserProvider,
    private events: Events,
    private nativeService: NativeService,
    private translate: TranslateService,
    private session: Session,
    public logger: Logger, 
    public modalCtrl: ModalController,
    public loadingCtrl: LoadingController, 
    public preloadProvider: PreloadProvider) {

  }

  /**
   * Send a POST request to our login endpoint with the data
   * the user entered on the form.
   */
  login(accountInfo: any) {
    accountInfo.password = Utility.md5(accountInfo.password);
    let seq = this.httpService.post('Auth/Login', false, accountInfo).share();
    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (data.errcode == 0) {
        this.loginSuccessHandle(data);
      } else {
        throw Error(data.errmsg);
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'login');
    });

    return seq;
  }

  LoginByWFAccount(accountInfo: any) {
    // accountInfo.password = Utility.md5(accountInfo.password);
    let seq = this.httpService.post('Auth/LoginByWFAccount', false, accountInfo).share();
    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (data.errcode == 0) {
        this.loginSuccessHandle(data);
      } else {
        throw Error(data.errmsg);
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'LoginByWFAccount');
    });

    return seq;
  }

  /**
   * Send a POST request to our signup endpoint with the data
   * the user entered on the form.
   */
  signup(accountInfo: any) {
    let seq = this.httpService.post('Auth/Register', false, {
      "mobilePhone": accountInfo.country + " " + accountInfo.mobilephone,
      "securityCode": accountInfo.verifyCode,
      "lang": this.session.language
    }).share();

    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (data.errcode == 0) {
        this.loginSuccessHandle(data,false,true);
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'signup');
    });

    return seq;
  }
  /*
    短信校验登录
  */
  onteLogin(accountInfo: any) {
    let seq = this.httpService.post('Auth/LoginBySecurityCode', false, {
      "mobilePhone": accountInfo.country + " " + accountInfo.mobilephone,
      "securityCode": accountInfo.verifyCode,
      "lang": this.session.language
    }).share();

    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (data.errcode == 0) {
        this.loginSuccessHandle(data,false,true);
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'onteLogin');
    });
    return seq;
  }

  /**
   * 登录成功处理
   */
  loginSuccessHandle(data, isHideLoading?: Boolean, isRegister?: Boolean) {
    this.session.userId = data.result.userId;
    this.session.token = data.result.userToken;
    this.session.expireTime = data.result.expireTime;
    if (data.result && data.result.refreshToken) {
      this.session.refreshToken = data.result.refreshToken;
      // this.storageService.setItem('refreshtoken', this.session.refreshToken);
    }
    // if(this.session.userId==null || this.session.userId==undefined) this.session.userId = data.result.userid;
    // if(this.session.token==null || this.session.token==undefined) this.session.token = data.result.usertoken;

    const tokenObject = { userId: '', userToken: '', refreshToken: '' }
    tokenObject.userId = this.session.userId;
    tokenObject.userToken = this.session.token;
    tokenObject.refreshToken = this.session.refreshToken;
    this.storageService.setItem('tokenObject', tokenObject);
    // this.storageService.setItem('token', this.session.token);
    this.storageService.getItem('enabled-file-cache-' + this.session.userId).then(res => {//获取是否启用缓存文件
      if (res === false) {
        this.session.enabledFileCache = false;
      }
    });
    //预加载是否需要签到
    // this.preloadProvider.IsPunchMember();

    //预加载社群栏目
    this.preloadProvider.getCommunityChannel();
    //预加载首页数据
    this.preloadProvider.getHomeData(isHideLoading);
    //预加载首页数据
    this.preloadProvider.getAllWidget(isHideLoading);
    //预加载个人信息
    this.preloadProvider.getUserInfo(isRegister);

    //更新用户设备Id
    //this.nativeService.showToast(this.session.deviceId);
    if (this.session.deviceId) {
      //this.nativeService.alert(this.session.deviceId);
      this.userProvider.updateDeviceId(this.session.deviceId).subscribe();
    }

    // 更新用户WeChatId
    
    // if (this.session.unionId) { 
    //   this.userProvider.BindWechatByUnionId(this.session.unionId).subscribe();
    // } 

  }

  /**
   * 刷新userToken
   */
  refreshUserToken() {
    let seq = this.httpService.post('Auth/RefreshUserToken', false, {
      "RefreshToken": this.session.refreshToken,
      "OldToken": this.session.token
    }).share();
    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      // if (data.errcode == 0) {
      //     this._loginSuccessHandle(data);
      // }
    }, err => {
      this.logger.log(JSON.stringify(err), 'refreshUserToken');
    });
    return seq;
  }


  /**
   * 发送短信验证码之前，先输入验证码
   * @param accountInfo 
   * @param countDownKey 
   */
  sendSecurityCodeBefore(inputType:string, accountInfo: any, countDownKey: string,callback: Function) { 
    let IdentifyCodeModal = this.modalCtrl.create('IdentifyCodePage', {
      inputType: inputType,
      clientId: accountInfo.clientId || accountInfo.country + " " + accountInfo.mobilephone,
      accountInfo: accountInfo,
      countDownKey: countDownKey,
    },{ 
      showBackdrop:true,
      cssClass:'identify-code-backdrop',
      enterAnimation: 'modal-scale-enter',
      leaveAnimation: 'modal-scale-leave'
    });

    IdentifyCodeModal.present()
    IdentifyCodeModal.onDidDismiss(()=>{
      callback && callback();
    }) 
  }
 
  /**
   * 发送验证码，由此方法统一处理异常。
   * @param accountInfo 用户信息
   */
  sendSecurityCode(accountInfo: any, countDownKey: string) {

    let seq = this.httpService.post('Auth/SendSecurityCode', true, {
      "mobilePhone": accountInfo.country + " " + accountInfo.mobilephone,
      "lang": this.session.language
    }).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("SIGNUP_VERIFYCODE_ISSENT"));
        this.events.publish('countDown:send-security-code:' + countDownKey);
      }
    }, err => {
      if (err.errcode == 6125) { // failed to send security code
        // this.sendCodeErrorString = err.errmsg
        this.nativeService.alert(this.translate.instant("VERIFICATION_CODE_SENT"))
      } else if (err.errcode == 6112) { // incorect phone number format 

      }

      this.logger.log(JSON.stringify(err), 'sendSecurityCode');
    });
    return seq;
  }

  setPassword(input: any) {
    //let seq = this.httpService.get('User/setPassword', true, input).share();

    let seq = this.httpService.post('User/setPassword', true, {
      "password": Utility.md5(input.password.toString())
    }).share();

    // seq.subscribe((data: any) => {
    //   this.session.user = data.result;
    // });Password

    seq.subscribe((data: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("SET_PASSWORD_ISSENT"));
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'setPassword');
    });

    return seq;
  }

 
  /** 获取AuthCode */
  getAuthCode(AppId, URL){ 
    if(!AppId)AppId = Utility.getQueryByName(URL,'appid') || "0"
    let seq = this.httpService.post('v2/Auth/GetCode', true, { AppId: AppId}).share();
    return seq;
  }

  /**
   * 登录微信
   * @param Code 微信code
   */
  LoginByWechat(Code: string) {
    let seq = this.httpService.post('Auth/LoginByWechat', true, {
      Code: Code
    }).share();
    return seq;
  }

  
  /**
   * 获取微信Code
   */
  getWeChatCode():Promise<any>{
    return new Promise((resolve, reject) => {
      if(this.nativeService.isMobile()){
        this.nativeService.isInstallWeChat(); 
        let loading = this.loadingCtrl.create({
          content: "跳转微信登录中...",//loading框显示的内容
          dismissOnPageChange: true, // 是否在切换页面之后关闭loading框
          showBackdrop: true  //是否显示遮罩层
        });
        loading.present();
        try {
          let scope = "snsapi_userinfo",
            state = "com.fihmb.iproud";
          // 1. 获取code
          Wechat.auth(scope, state, (response) => { 
            resolve(response.code)
          }, (reason) => { 
            reject(reason)
          });
        } catch (error) {
          console.error(error);
          reject(error)
        } finally {
          loading.dismiss();
        }
      }else{
        resolve("061QKBZ12AhrQW0pY4Z12gVmZ12QKBZd")
      }
      
    }) 
  }


  /**
   * 获取验证码
   * @param clientId 客户端id，可传手机号码
   */
  MessageCaptcha(clientId) {
    let seq = this.httpService.post('Message/Captcha', false, { "clientId": clientId }, true).share();
    seq.subscribe((data: any) => {
    });
    return seq;
  }

  /**
   * 登录，需要验证码
   * @param accountInfo 账号信息
   * @param Captcha 图形验证码
   */
  Login2(accountInfo, Captcha: string) {
    
    let form = Utility.deepCopy(accountInfo)
    form.password = Utility.md5(form.password);
    form.Captcha = Captcha;
    let seq = this.httpService.post('v2/Auth/Login', false, form, false).share();
    seq.subscribe((data: any) => {
      setTimeout(()=>{
        this.loginSuccessHandle(data);
        // this.events.publish('Login2:success', data);
      },300)
    }, err => { 
    });
    return seq;
  }

  /**
   * 获取手机验证码，需要传图形验证码
   * @param accountInfo 账号信息  
   * @param Captcha 验证码
   */
  SendSecurityCode2(accountInfo: any, Captcha: string, countDownKey: string) {
    let seq = this.httpService.post('v2/Auth/SendSecurityCode', false, {
      "Lang": this.session.language,
      "MobilePhone": accountInfo.country + " " + accountInfo.mobilephone,
      "Captcha": Captcha
    }, true).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        setTimeout(()=>{
          this.nativeService.showToast(this.translate.instant("SIGNUP_VERIFYCODE_ISSENT"));
          this.events.publish('countDown:send-security-code:' + countDownKey);
        },300)
      }
    }, err => {
      //只处理获取手机验证码错误，图形验证码交由验证码页面处理
      if (err.errcode == ErrorCode.USER.SEND_SECURITY_CODE_FAILED) { // failed to send security code
        // this.sendCodeErrorString = err.errmsg
        this.nativeService.alert(this.translate.instant("VERIFICATION_CODE_SENT"))
      } else if (err.errcode == ErrorCode.USER.MOBILE_FORMAT_ERROR) { // incorect phone number format 
        this.nativeService.alert("手机格式不正确")
      }
    });
    return seq;
  }



  /**
   * 获取手机验证码，需要传图形验证码
   * @param accountInfo 账号信息
   * @param Captcha 图形验证码
   */
  LoginByWFAccount2(accountInfo: any, Captcha: string) {
    accountInfo.Captcha = Captcha
    let seq = this.httpService.post('v2/Auth/LoginByWFAccount', false, accountInfo, false).share();
    seq.subscribe((data: any) => {
      setTimeout(()=>{
        this.loginSuccessHandle(data);
        // this.events.publish('LoginByWFAccount2:success', data);
      },300)
    }, err => { 
    });
    return seq;
  }

} 


