import { HandleProvider } from './../providers/handle/handle';
import { NativeService } from './native-service';
import { SQLITE } from './constants'; 
import { Injectable } from '@angular/core'; 
import { SQLite, SQLiteObject, SQLiteTransaction } from '@ionic-native/sqlite';
import { Observable } from 'rxjs';


@Injectable()
export class SqliteService {
 
  private win: any = window;  //window对象  
  private database: SQLiteObject; //数据库对象
 
  constructor( public sqlite: SQLite,
    public nativeService: NativeService,
    public handle: HandleProvider ) {
  }

  /**
   * 初始化db                                             
   * @param userId 用户id
   */
  init(userId: string, sqls: Array<{sql: string, params: any}>): Observable<{ result: boolean, msg: any }> {
    return new Observable(observe => { 
      this.createDB(userId).subscribe(() => { 
        this.initTable(sqls).then(() => {
          observe.next({ result: true, msg: '初始化表成功' })
        }).catch(err => {
          observe.error({ result: false, msg: err })
        });
      }, err => {
        observe.error({ result: false, msg: err })
      })
    })
  }

  /**
   * 创建数据库；
   * 以用户id为数据库名称进行创建
   * @param userId 
   */
  createDB(userId: string): Observable<{ result: boolean, msg: any }> {
    return new Observable(observe => {
      if (!userId) {
        console.error("userId is empty, can not create database") 
        observe.error({ result: false, msg: "userId is empty, can not create database" })
        return;
      }
      userId = userId.toString()
      if (!this.win.sqlitePlugin) {
        //window.openDatabase("数据库名字", "版本","数据库描述",数据库大小);
        this.database = this.win.openDatabase(userId || SQLITE.WINDOW.name, SQLITE.WINDOW.version,
          SQLITE.WINDOW.description, SQLITE.WINDOW.maxSize);
        observe.next({ result: true, msg: 'openDatabase successfully' })
        return;
      }
      if (this.nativeService.isAndroid()) {
        this.sqlite.create({
          name: userId || SQLITE.ANDROID.name,
          location: SQLITE.ANDROID.location
        }).then((db) => {
          this.database = db; 
          if(!db || !db.transaction){
            observe.error({ result: false, msg: 'db is empty' }) 
          }else{
            observe.next({ result: true, msg: 'createDatabase successfully' })
          }
          
        }).catch(err => { 
          observe.error({ result: false, msg: err }) 
        });
      } else {
        this.sqlite.create({
          name: userId || SQLITE.IOS.name,
          iosDatabaseLocation: SQLITE.IOS.iosDatabaseLocation,
        }).then((db) => {
          this.database = db;
          if(!db || !db.transaction){
            observe.error({ result: false, msg: 'db is empty' }) 
          }else{
            observe.next({ result: true, msg: 'createDatabase successfully' })
          }
          
        }).catch(err => {
          observe.error({ result: false, msg: err }) 
        });
      }
    })


  }


  /**
   * 执行sql语句
   * @param {string} sql
   * @param {*} [params={}]
   * @returns {Promise<any>}
   * @memberof SQLService
   */
  executeSql(sql: string, params: any = []): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        this.database.transaction((tx: SQLiteTransaction) => {
          tx.executeSql(sql, params, (tx, res) => {
            let data = []
            if (res.rows.length > 0) {
              for (let i = 0; i < res.rows.length; i++) {
                data.push(res.rows.item([i]))
              }
            } 
            this.handle.generate(` ${sql}`, 1,
              res ? {
                params: params,
                rows: data,
                rowsAffected: res['rowsAffected']
              } : res)
            res['data'] = data;
            resolve({ tx: tx, res: res }); 
          }, (tx, err) => {
            console.error(err)
            this.handle.generate(` ${sql} error`, -1, { 
              code: err.code, 
              message: err.message,
              params: params,
              sql: sql })
            reject({ tx: tx, err: err });
          });

        });
      }
      catch (err) {
        reject({ err: err }); 
        this.handle.generate(` ${sql} error`, -1 ,{ 
          code: err.code, 
          message: err.message,
          params: params,
          sql: sql })
      }
    });
  }

  /**
   * 初始化表，不存在则创建
   */
  initTable(sqls: Array<{sql: string, params: any}>): Promise<any> { 
    return new Promise((resolve, reject) => {
      try {
        this.database.transaction((tx: SQLiteTransaction) => {
          for(let sqlItem of sqls){
            tx.executeSql(sqlItem.sql, sqlItem.params, (tx, res) => {
              resolve({ tx: tx, res: res });
            }, (tx, err) => {
              reject({ tx: tx, err: err });
            });
          } 
        });
      }
      catch (err) {
        reject({ err: err });
      }
    });
  }


  /**
   * 关闭数据库（用户退出登陆时）
   */
  closeUserDataBase() {
    if (this.nativeService.isMobile()) {
      this.database.close(); 
    }
  }
   
}


