import { Injectable } from '@angular/core';

interface BoardColumn {
  id: number
  displayName: string
  code: string
}
@Injectable()
export class Session {

  private _userId: string;//用户id
  private _username: string;//用户名
  private _user;//用户详细信息
  private _token: string;//token
  private _refreshToken: string;//refreshToken
  private _expireTime: number;//user token 有效时间 
  private _user_and_token: object;

  private _language: string;//Language Code
  private _deviceId: string;//设备id
  private _shortDatePattern: string;//简短日期格式
  private _longDatePattern: string;//完整日期格式
  private _timezone: string;//时区
  private _utcOffset: number;//时区偏移秒数
  private _dstOffset: number;//夏令时偏移秒数
  private _isEmployee: Boolean;//是否为员工身份
  private _isPunchMember: Boolean;
  private _homedata: object;//首页数据
  private _widget: any[];//应用数据
  private _setting: any;//个人设置
  private _messageWidgetId: any;//消息分组id
  //设置http请求是否显示loading,注意:设置为true,接下来的请求会不显示loading,请求执行完成会自动设置为false
  private _showLoading: boolean = true;
  private _NightTheme: boolean = false;
  //是否启用文件缓存
  private _enabledFileCache: boolean = true;
  private _globalSetting: any; //全局设置
  private _LangContexts: any; //暂取widgetName,iProudName
  private _keyboardHeight: number = 0;
  private _showSocial: boolean; // 显示社群tabs 
  private _communityColumn: BoardColumn;
  private _unionId: string; // 微信uniondId
  get userId(): string {
    return this._userId;
  }

  set userId(value: string) {
    this._userId = value;
  }

  get username(): string {
    return this._username;
  }

  set username(value: string) {
    this._username = value;
  }

  get user() {
    return this._user;
  }

  set user(value) {
    this._user = value;
  }

  get token(): string {
    return this._token;
  }

  set token(value: string) {
    this._token = value;
  }

  get refreshToken(): string {
    return this._refreshToken;
  }

  set refreshToken(value: string) {
    this._refreshToken = value;
  }

  get expireTime(): number {
    return this._expireTime;
  }

  set expireTime(value: number) {
    this._expireTime = value;
  }

  get tokenObject(): object {
    return this._user_and_token;
  }

  set tokenObject(value: object) {
    this._user_and_token = value;
  }

  get language() {
    return this._language;
  }

  set language(value) {
    this._language = value;
  }
  get deviceId(): string {
    return this._deviceId;
  }

  set deviceId(value: string) {
    this._deviceId = value;
  }
  //简短日期格式
  get shortDatePattern(): string {
    return this._shortDatePattern;
  }
  set shortDatePattern(value: string) {
    this._shortDatePattern = value;
  }

  //完整日期格式
  get longDatePattern(): string {
    return this._longDatePattern;
  }
  set longDatePattern(value: string) {
    this._longDatePattern = value;
  }
  //时区
  get timezone(): string {
    return this._timezone;
  }
  set timezone(value: string) {
    this._timezone = value;
  }
  //时区偏移秒数
  get utcOffset(): number {
    return this._utcOffset;
  }
  set utcOffset(value: number) {
    this._utcOffset = value;
  }
  //夏令时偏移秒数
  get dstOffset(): number {
    return this._dstOffset;
  }
  set dstOffset(value: number) {
    this._dstOffset = value;
  }

  get showLoading(): boolean {
    return this._showLoading;
  }

  set showLoading(value: boolean) {
    this._showLoading = value;
  }
  get enabledFileCache(): boolean {
    return this._enabledFileCache;
  }

  set enabledFileCache(value: boolean) {
    this._enabledFileCache = value;
  }

  get homedata(): Object {
    return this._homedata;
  }

  set homedata(value: Object) {
    this._homedata = value;
  }

  get isPunchMember(): Boolean {
    return this._isPunchMember;
  }

  set isPunchMember(value: Boolean) {
    this._isPunchMember = value;
  }

  get isEmployee(): Boolean {
    return this._isEmployee;
  }

  set isEmployee(value: Boolean) {
    this._isEmployee = value;
  }

  get widget() {
    return this._widget;
  }

  set widget(value) {
    this._widget = value;
  }


  get setting() {
    return this._setting;
  }

  set setting(value) {
    this._setting = value;
  }
  get messageWidgetId() {
    return this._messageWidgetId;
  }

  set messageWidgetId(value) {
    this._messageWidgetId = value;
  }

  get isDarkMode() {
    return this._NightTheme;
  }

  set isDarkMode(value: boolean) {
    this._NightTheme = value;
  }

  get globalSetting() {
    return this._globalSetting;
  }

  set globalSetting(value: any) {
    this._globalSetting = value;
  }

  get LangContexts() {
    return this._LangContexts;
  }

  set LangContexts(value: any) {
    this._LangContexts = value;
  }


  get keyboardHeight() {
    return this._keyboardHeight;
  }

  set keyboardHeight(value: number) {
    this._keyboardHeight = value;
  }

  get showSocial() {
    return this._showSocial;
  }

  set showSocial(value: boolean) {
    this._showSocial = value;
  }

  get communityColumn() {
    return this._communityColumn;
  }

  set communityColumn(value: BoardColumn) {
    this._communityColumn = value;
  }

  get unionId() {
    return this._unionId;
  }

  set unionId(value: string) {
    this._unionId = value;
  }

  get certKey() {
    return this.userId + 'cert';
  }


}


