import { Logger } from './../services/logger';
import { NativeService } from './../services/native-service';

import { StorageService } from './../services/storage-service';
import { Injectable } from '@angular/core';
import { DEVELOPMENT_WEB_SERVER, DEVELOPMENT_API_SERVER, DEVELOPMENT_FILE_SERVER, DEVELOPMENT_APP_DOWNLOAD_PAGE, APPLE_STORE_APPID_TEST, IS_DEBUG } from '../services/constants';
import { PRODUCTION_WEB_SERVER, PRODUCTION_API_SERVER, PRODUCTION_FILE_SERVER, PRODUCTION_APP_DOWNLOAD_PAGE, APPLE_STORE_APPID } from '../services/constants';
import { Utility } from '../services/utility';


@Injectable()
export class ServerProvider {
    isDebug: boolean = IS_DEBUG;
    constructor(private storageService: StorageService,
        private nativeService: NativeService,
        public logger: Logger) {

    }
    async init() {
        try {
            const isdebug = await this.storageService.getItem("isdebug");
            const version = await this.storageService.getItem("version");

            this.nativeService.getVersionNumber().subscribe(currentVersionNo => {
                if (Utility.checkNewVersion(currentVersionNo, version)) {
                    this.isDebug = IS_DEBUG;
                } else if (typeof (isdebug) == 'boolean') {
                    this.isDebug = isdebug;
                }
            }, err => { 
                if (typeof (isdebug) == 'boolean') {
                    this.isDebug = isdebug;
                }
                this.logger.log(JSON.stringify(err), "this.nativeService.getVersionNumber()");
            });

        } catch (err) {
            this.logger.log(JSON.stringify(err), "this.storageService.getItem()");
        }

    }


    WebServer() {
        if (this.isDebug) {
            return DEVELOPMENT_WEB_SERVER;
        } else {
            return PRODUCTION_WEB_SERVER;
        }
    }
    // 根据this.isDebug标记取得开发或生产环境，API服务器地址
    ApiServer() {
        return this.BaseApiServer() + "v1/"; 
    }

     // 根据this.isDebug标记取得开发或生产环境，API服务器地址
     BaseApiServer() {
        if (this.isDebug) {
            return DEVELOPMENT_API_SERVER;
        } else {
            return PRODUCTION_API_SERVER;
        }
    }

    // 根据this.isDebug标记取得开发或生产环境, 文件服务器地址
    FileServer() {
        if (this.isDebug) {
            return DEVELOPMENT_FILE_SERVER;
        } else {
            return PRODUCTION_FILE_SERVER;
        }
    }
    // 根据this.isDebug标记, 取得App下载页面地址
    AppDownloadPage() {
        if (this.isDebug) {
            return DEVELOPMENT_APP_DOWNLOAD_PAGE;
        } else {
            return PRODUCTION_APP_DOWNLOAD_PAGE;
        }
    }

    // 根据this.isDebug标记取得开发或生产环境, 文件服务器地址
    ServiceAgreement() {
        if (this.isDebug) {
            return DEVELOPMENT_APP_DOWNLOAD_PAGE.replace('AppClient', '');
        } else {
            return PRODUCTION_APP_DOWNLOAD_PAGE.replace('AppClient', '');;
        }
    }
    // 根据this.isDebug标记, 取得App下载页面地址
    PrivacyPolicy() {
        if (this.isDebug) {
            return DEVELOPMENT_APP_DOWNLOAD_PAGE.replace('AppClient', '');
        } else {
            return PRODUCTION_APP_DOWNLOAD_PAGE.replace('AppClient', '');;
        }
    }
    // 根据this.isDebug标记, 取得App下载页面地址

    AppStoreId() {
        if (this.isDebug) {
            return APPLE_STORE_APPID_TEST;
        } else {
            return APPLE_STORE_APPID;
        }
    }
}


