  //   let devalue = currentTime - dateTime.getTime();

  //   if(devalue > 0  && devalue < 1000 * 60 * 60 *24 ){ 
  //     if(dateTime.getDay() != currentDate.getDay()){
  //       return Utility.dateFormat(dateTime,"MM/dd HH:mm:ss");  //第二天显示月日时间
  //     }else{
  //       return Utility.dateFormat(dateTime,"HH:mm:ss");  //当天只显示时间
  //     }
  //   }else if(devalue >= 1000 * 60 * 60 * 24  && devalue < 1000 * 60 * 60 * 24 * 30){//一天以上，一个月内 显示月日和时间
  //     return Utility.dateFormat(dateTime,"MM/dd HH:mm:ss");  
  //   }else if(devalue >= 1000 * 60 * 60 * 24 * 30 ){  
  //     if(dateTime.getFullYear() != currentDate.getFullYear()){
  //       return Utility.dateFormat(dateTime,"MM/dd HH:mm:ss");  //第二年显示年月日时间
  //     }else{
  //       return Utility.dateFormat(dateTime,"yyyy/MM/dd HH:mm:ss");  //当年只显示月日时间
  //     }

