import { STORAGE_KEY } from './../../services/constants';
import { NativeService } from './../../services/native-service';
import { StorageService } from './../../services/storage-service';
import { Utility } from './../../services/utility'; 
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core'; 
import { IS_DEBUG, IS_BETA } from '../../services/constants';

/*
  Generated class for the HandleProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
 
@Injectable()
export class HandleProvider {
  history: any[] = [];
  isOnlyRecordError: boolean = true;
  Key: string = 'Handle:History'
  constructor(public http: HttpClient,
    public storageService: StorageService,
    public nativeService: NativeService ) { 

  }

  async init(){
    if(!this.history)this.history=[] 
    this.isOnlyRecordError = await this.storageService.getItem(STORAGE_KEY.SETTING.ONLY_COLLECT_ERROR_INFO)
    let history = await this.storageService.getItem(this.Key) 
      if(history){ 
        if(this.history.length > 0){
          this.history = this.history.concat(history);
          this.storageService.setItem(this.Key, this.history);
        } else{
          this.history = history;
        }
      }  
  }

  generateError(handleName: string,result? :any){
    this.generate(handleName, -1 ,result)
  }

  generateInfo(handleName: string,result? :any){
    this.generate(handleName, 0 ,result)
  }

  generateSql(handleName: string,result? :any){
    this.generate(handleName, 1 ,result)
  }

  /**
   * 
   * @param handleName 动作名称
   * @param type 类型 (0: normal; 1: sqlite; -1 : error) 
   * @param result 结果
   */
  generate(handleName: string, type: number ,result? :any){
    if((this.isOnlyRecordError && type != -1) || !IS_BETA)return;
    if(!this.history)this.history=[]
    let date = new Date()
    let time = date.getTime();
    let item = {
      handleName: handleName,
      handleTime: Utility.dateFormat(new Date(),'yyyy-MM-dd HH:mm:ss') + ' ' + time.toString().substr(time.toString().length-3, 3),
      createTime: time,
      type: type,
      result: result
    }
    this.history.push(item)

    // this.nativeService.writeLog(JSON.stringify(item, null ,2));
    this.storageService.setItem(this.Key, this.history);
  }

  get(type){ 
    let  history = this.filter(type); 
    history.sort((a, b)=>{
      return new Date(b.createTime).getTime() - new Date(a.createTime).getTime()
    })
    return history;
  }

  filter(type){
    let array = []
    if(!type || type == 999){
      return this.history
    }
    for(let item of this.history){ 
      if(item.type == type){
        array.push(item)
      }
    }
    return array
  }

  clear(){
    this.history = [];
    this.storageService.setItem(this.Key, []);
  }
}



