import { IDLApplyInfo } from './../models/IDLApplyInfo';
 
import { StorageService } from './../services/storage-service';
import { NativeService } from './../services/native-service';
import { Events } from 'ionic-angular';

import { Injectable } from '@angular/core'; 
import { HttpService } from '../services/http-service';
import { Session } from '../services/session'; 

@Injectable()
export class IDLRecruitingProvider {

  constructor(private httpService: HttpService,
    private session: Session,
    public events: Events,
    public nativeService: NativeService,
    public storageService: StorageService,) { 
    }
    PostTypes() {
      let seq = this.httpService.post('IDLRecruiting/PostTypes', true,{ "lang": this.session.language }).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    } 

    WorkSites() { 
      let seq = this.httpService.post('IDLRecruiting/WorkSites', true,{ "lang": this.session.language }).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    } 
    
    PostList(TenantId?, JobTypeId?, Skip?:number, Take?:number,isHideLoading?:Boolean ) { 

       let  PostsBaseInfo   =  {"Skip":Skip,"Take":Take,"Lang":"","TenantId":"","JobTypeId":"","SalaryFrom":"","SalaryTo":"","PostKey":""}
       PostsBaseInfo.Lang = this.session.language;
       PostsBaseInfo.TenantId = TenantId || '';
       PostsBaseInfo.JobTypeId = JobTypeId ||   '';
      let seq = this.httpService.post('IDLRecruiting/PostList', true, PostsBaseInfo,isHideLoading).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    } 
    
    PostDetail(PostId,isHideLoading?:Boolean) { 
      let seq = this.httpService.post('IDLRecruiting/PostDetail', true,{ "lang": this.session.language , "PostId": PostId},isHideLoading).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    } 
     
    
}



