import { Pipe, PipeTransform } from '@angular/core';

export interface cacheEntity{
  unit: string,
  value: number
}
/**
 * Generated class for the CacheFormatPipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
  name: 'cacheFormat',
})
export class CacheFormatPipe implements PipeTransform {
  /**
   * Takes a value and makes it lowercase.
   */
  transform(value: number, ...args) {
    if(!(value > 0))return ''
     return this._toString(value)
  }

  _transformToCacheObj(value: number){
    let result = {
      unit: '',
      value: 0
    }
    if (value > 0 && value < 1024) { 
      result.unit = 'B' ;
      result.value = value;
    } else if (value >= 1024 && value < 1024 * 1024) {
      result.unit = 'KB' ;
      result.value = value / 1024; 
    } else if (value >= 1024 * 1024 && value < 1024 * 1024 * 1024) {
      result.unit = 'MB' ;
      result.value = value / 1024 / 1024;  
    } else if (value >= 1024 * 1024 * 1024) {
      result.unit = 'GB' ;
      result.value = value / 1024 / 1024 / 1024; 
    }   
    return result
  }
  _toFixed(cache: number = 0) {
    return cache.toFixed(2)
  }

  _toString(value: number){
    let result: cacheEntity = this._transformToCacheObj(value);

    return `(${this._toFixed(result.value)}${result.unit})`
  }
}



