import { WEIXIN_APPID, WEIXIN_APPSECRET } from './../services/constants';
import { Logger } from './../services/logger'; 

import { Injectable } from '@angular/core';
import { Events, LoadingController } from "ionic-angular";
import { HttpService } from '../services/http-service';
import { TranslateService } from '@ngx-translate/core';
import { Session, StorageService, Utility, NativeService } from '../services/services';
import { Observable } from 'rxjs';
import { ExtendField } from '../models/userinfo';

@Injectable()
export class UserProvider {
  constructor(private httpService: HttpService,
    public events: Events,
    private translate: TranslateService,
    private nativeService: NativeService,
    private storageService: StorageService,
    private session: Session,
    public logger: Logger,
    public loadingCtrl: LoadingController
    ) {

  }

  /**
   * Log the user out, which forgets the session
   */
  logout() {
    let seq = this.httpService.get('User/Logout', true, {}).share();
    this.storageService.removeItem("userId");
    this.storageService.removeItem('token');
    this.storageService.removeItem('refreshtoken');
    this.storageService.removeItem('tokenObject');
    this.storageService.removeItem('homedata');
    this.session.token = null;
    this.session.userId = null;
    this.session.user = null;
    this.session.refreshToken = null;
    this.session.homedata = null;
    this.session.unionId = null;
    // 不直接做页面跳转，发布事件，由TabsPage接收事件并处理
    this.events.publish('user:reLogin', Date.now());
    return seq;
  }

  uploadAvatar(imageData: string) {
    let seq = this.httpService.post('User/UploadAvatar', true, { "imageData": imageData }).share();
    seq.subscribe((data: any) => {
      this.getUserInfo();
    });
    return seq;
  }

  // getUserInfo(){
  //   return this.httpService.post('User/GetUserInfo',true).map((res: Response) => res.json());
  // }
  getUserInfo(isHideLoading?: Boolean, isRegister?: Boolean) {
    let seq = this.httpService.post('User/GetUserInfo', true, { "Lang": this.session.language }, isHideLoading).share();
    seq.subscribe((data: any) => {
      this.session.user = data.result; 
      this.session.isEmployee = data.result.isEmployee;
      // if(this.session.isEmployee) {
      //   this.session.user.extendFields = []
      //   for(let key of Object.keys(this.session.user.employeeInfo)){
      //     let item = {
      //       name: key,
      //       value: this.session.user.employeeInfo[key],
      //       IsLongField: this.session.user.employeeInfo[key] ? this.session.user.employeeInfo[key].length > 10 : false
      //     }
      //     this.session.user.extendFields.push(item)
      //   }
      // }
      this.storageService.setItem("isEmployee", data.result.isEmployee)
      this.events.publish('user:info-changed', Date.now());
      if (isRegister) {
        this.events.publish('register:success', Date.now());
      }
    }, error => {
      if (error.errcode === 6101) {
        this.events.publish('user:reLogin')
      }
    });
    return seq;
  }

  changePassword(input: any) {
    input.oldPassword = Utility.md5(input.oldPassword);
    input.newPassword = Utility.md5(input.newPassword);
    let seq = this.httpService.post('User/ResetPassword', true, input).share();
    seq.subscribe((data: any) => {

    }, err => {
      if (err.errcode == 6122) {
        this.nativeService.alert(this.translate.instant("SET_PASSWORD_OLD_ERR"));
      }
    });

    return seq;
  }

  //设置昵称
  setNickname(input: any) {

    let seq = this.httpService.post('User/UpdateUserInfo', true, {
      "nickname": input.nickname,
      "Lang": this.session.language
    }).share();

    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("MY_SET_NICKNAME_SUCCEED"));
      }
    }, err => {
      this.nativeService.showToast(this.translate.instant("MY_SET_NICKNAME_DEFEATED"));

      this.logger.log(JSON.stringify(err), "User/UpdateUserInfo");
    });

    return seq;
  }

  //重置电话
  setPhone(input: any) {

    let seq = this.httpService.post('User/UpdateMobilePhone', true, {
      "newMobilePhone": input.phoneNo,
      "securityCode": input.securityCode,
      "Lang": this.session.language
    }).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("MY_SET_PHONE_SUCCEED"));
      }
    }, err => {
      this.nativeService.showToast(this.translate.instant("MY_SET_PHONE_DEFEATED"));
      this.logger.log(JSON.stringify(err), "User/UpdateMobilePhone");
    });

    return seq;
  }
  //绑定员工信息
  BindEmployee(input: any) {
    let seq = this.httpService.post('User/BindEmployee', true, {
      "employeeNo": input.employeeNo,
      "idCardNo": input.idCardNo,
      "Lang": this.session.language
    }).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("MY_BIND_EMPLOYEE_SUCCEED"));
      }
    }, err => {

      let errorstr = ''
      switch (err.errcode) {
        case 6143: errorstr = this.translate.instant("MY_BIND_EMPLOYEE_ALREADY_BIND") + "," + err.errmsg;
          break;
        case 6141: errorstr = this.translate.instant("MY_BIND_EMPLOYEE_DEFEATED");
          break;
        default:
          errorstr = err.errmsg
      }



      this.nativeService.showToast(errorstr);
      this.logger.log(JSON.stringify(err), "User/BindEmployee");
    });
    return seq;
  }
  //解除员工信息绑定
  UnbindEmployee() {
    let seq = this.httpService.post("User/UnbindEmployee", true, { "Lang": this.session.language }).share();
    seq.subscribe((data: any) => {
      this.storageService.removeItem(this.session.certKey);
      if (data.errcode == 0) {
        this.nativeService.showToast(this.translate.instant("MY_UNBIND_EMPLOYEE_SUCCEED"));
      }
    }, err => {
      this.nativeService.showToast(this.translate.instant("MY_UNBIND_EMPLOYEE_DEFEATED"));
      this.logger.log(JSON.stringify(err), "User/UnbindEmployee");
    });
    return seq;
  }

  updateDeviceId(deviceId: any) {
    //this.nativeService.showToast("updateDeviceId begin");
    let seq = this.httpService.post('User/DeviceId', true, { "deviceId": deviceId }, true).share();
    seq.subscribe((data: any) => {
      //this.nativeService.showToast("updateDeviceId completed");
    });
    return seq;
  }


  privateSetting(setting: any) {
    //this.nativeService.showToast("updateDeviceId begin");
    let seq = this.httpService.post('User/PrivateSetting', true, setting, true).share();
    seq.subscribe((data: any) => {
    });
    return seq;
  }

  //取私有設置
  getPrivateSetting() {
    let seq = this.httpService.get('User/Settings', true, {
    },true).share();
    seq.subscribe((data: any) => {
    }, err => {
    });
    return seq;
  }


  /**
   * 保存用户坐标
   * @param location 
   */
  saveGeolocation(location: any) {
    let seq = this.httpService.post('User/Geolocation', true, location, true).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }

  /**
   * 清除服务器缓存
   */
  clearMemberCache() {
    let seq = this.httpService.post('User/ClearMemberCache', true).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }

  /**
   * 是否需要sign
   */
  IsPunchMember() {
    let seq = this.httpService.post('User/IsPunchMember', true, null, true).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }

  PunchMemberRecords() {
    let seq = this.httpService.post('User/PunchMemberRecords', true).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }
  

  /**
   * 解绑微信 
   */
  UnBindWechat() {
    let seq = this.httpService.post('User/UnBindWechat', true, {}).share();
    seq.subscribe((data: any) => { 
      this.getUserInfo(true).subscribe()
    });
    return seq;
  }
  /**
   * 绑定微信
   * @param Code 微信code
   */
  BindWechat(Code) {
    let seq = this.httpService.post('User/BindWechat', true, {
      Code: Code
    }).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }
  

    /**
   * 绑定微信
   * @param UnionId 微信 UnionId
   */
  BindWechatByUnionId(UnionId) {
    let seq = this.httpService.post('User/BindWechatByUnionId', true, {
      UnionId: UnionId
    }).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }

    /**
     * 注销账号
     */
    CancelAccount() {
    let seq = this.httpService.post('User/Cancel', true, {
    }).share();
    seq.subscribe((data: any) => { 
    });
    return seq;
  }
 
  
  // GlobleSettings() {
  //   let seq = this.httpService.get('Config/GlobleSettings', true, null, true).share();
  //   seq.subscribe((data: any) => { 
  //   });
  //   return seq;
  // }

  getWechatUserInfo(_code): Observable<any>{ 
    return new Observable(obersve=>{
      let grant_type = 'authorization_code';
      let code = _code;
      let getTokenUrl = `https://api.weixin.qq.com/sns/oauth2/access_token?appid=${WEIXIN_APPID}&secret=${WEIXIN_APPSECRET}&code=${code}&grant_type=${grant_type}`;
   
      this._getWechatAccessToken(getTokenUrl).subscribe(res=>{
        if(res.errcode == 0){
          let accessTokenResponse = res.result;
          var accessToken = accessTokenResponse.access_token;
          var openId = accessTokenResponse.openid; 
          // 3. 获取用户信息
          let getUserUrl = `https://api.weixin.qq.com/sns/userinfo?access_token=${accessToken}&openid=${openId}&lang=zh_CN`; 
          this._getWechatUserInfo(getUserUrl).subscribe(res=>{
            obersve.next(res)
          },error=>{
            obersve.error(error)
          }); 
        } 
      },err=>{
        obersve.error(err)
      })
    }) 
  }
 
  /**
   * 获取微信Code
   */
  getWeChatCode(){
    return new Promise((resolve, reject) => {
      this.nativeService.isInstallWeChat(); 
      let loading = this.loadingCtrl.create({
        content: "跳转微信登录中...",//loading框显示的内容
        dismissOnPageChange: true, // 是否在切换页面之后关闭loading框
        showBackdrop: true  //是否显示遮罩层
      });
      loading.present();
      try {
        let scope = "snsapi_userinfo",
          state = "com.fihmb.iproud";
        // 1. 获取code
        Wechat.auth(scope, state, (response) => { 
          resolve(response.code)
        }, (reason) => { 
          reject(reason)
        });
      } catch (error) {
        console.error(error);
        reject(error)
      } finally {
        loading.dismiss();
      }
    }) 
  }

  _getWechatAccessToken(getTokenUrl){
    let seq = this.httpService.post(getTokenUrl).share()
    seq.subscribe(()=>{

    },err=>{
      alert('Get AccessToken failed,' + JSON.stringify(err, null ,2))
    })
    return seq
  }

  _getWechatUserInfo(getUserUrl){
    let seq = this.httpService.post(getUserUrl).share()
    seq.subscribe(()=>{

    },err=>{
      alert('Get WeChatUserInfo failed,' + JSON.stringify(err, null ,2))
    })
    return seq
  }

}



