  /**
   * 每次调用sequence加1
   * @type {()=>number}
   */
  static getSequence = (function () {
    let sequence = 1;
    return function () {
      return ++sequence;
    };
  })();

  /**
   * 返回字符串长度，中文计数为2
   * @param str
   * @returns {number}
   */
  static strLength(str: string): number {
    let len = 0;
    for (let i = 0, length = str.length; i < length; i++) {
      str.charCodeAt(i) > 255 ? len += 2 : len++;
    }
    return len;
  }

  /**
   * 把url中的双斜杠替换为单斜杠
   * 如:http://localhost:8080//api//demo.替换后http://localhost:8080/api/demo
   * @param url
   * @returns {string}
   */
  static formatUrl(url: string = ''): string {
    let index = 0;
    if (url.startsWith('http')) {
      index = 7
    }
    return url.substring(0, index) + url.substring(index).replace(/\/\/*/g, '/');
  }

  /**
   * 字符串加密
   * @param str
   * @returns {any}
   */
  static md5(str: string) {
    let text = Md5.hashStr(str).toString();
    text = Md5.hashStr(text).toString();
    return text;
  }

  static generateSignature() {
    let timestamp = new Date().getTime().toString()
    let nonce = Math.floor((Math.random() * 9 + 1) * 100000).toString();

    let array = [APPSECRET, timestamp, nonce];
    array.sort()

    let res = {
      'Signature': this.md5(array[0] + array[1] + array[2]),
      'Timestamp': timestamp,
      'Nonce': nonce
    }
    return res;
  }

  /** 产生一个随机的32位长度字符串 */
  static uuid() {
    let text = "";
    let possible = "abcdef0123456789";
    for (let i = 0; i < 19; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text + new Date().getTime();
  }

