  static removeSessionInfo(link: string): string {
    if(!link)return '';
    let url = link; 
    let questionIndex = url.indexOf('?');
    let anchorIndex = url.indexOf('#'); 
    if(questionIndex == -1){
      return link;
    }else if(anchorIndex > -1 && questionIndex < anchorIndex){
      let partOfAnchor = url.substring(anchorIndex, url.length)
      let partOfPrefix = url.substring(0, anchorIndex)  
      return this._removeKeys(partOfPrefix) + partOfAnchor
    }else{
      let partOfPrefix = url 
      return this._removeKeys(partOfPrefix)
    }
 
  }

  static _removeKeys(url: string){
    let index = url.indexOf('?') 
    let partOfPrefix = url.substring(0, index)
    let params =  this.parseQuery(url)
    let keys = ['Signature','AuthCode','Theme','From','Nonce','Timestamp','accountId','AppId','Lang'] 
    for(let key of keys){
      delete params[key] //移除签名 
    } 
    for(let key of Object.keys(params)){
      if(partOfPrefix.indexOf('?') > -1){
        partOfPrefix += `&${key}=${params[key]}`;
      }else{
        partOfPrefix += `?${key}=${params[key]}`;
      } 
    }
    return partOfPrefix
  }
  
  /**
   * 移除AuthCode等个人信息
   * @param link 
   */
  static removeSessionInfoForShareLink(link: string): string {
    let url = link;
    let index = url.indexOf('?')
    if (url && index > -1) {

      let preurl = url.substring(0, index)
      let params =  this.parseQuery(url)

      delete params['Signature'] //移除签名
      delete params['AuthCode'] //移除authCode
      delete params['Theme'] //移除Theme
      delete params['From'] //移除From
      delete params['Nonce'] //移除Nonce 
      delete params['Timestamp'] //移除Timestamp
      delete params['accountId'] //移除accountId

      for(let key of Object.keys(params)){
        if(preurl.indexOf('?') > -1){
          preurl += `&${key}=${params[key]}`;
        }else{
          preurl += `?${key}=${params[key]}`;
        }
        
      }
      return preurl;  
    }
    return url
  }
  

  /**
   * 更换参数的值，若不存在该参数则新增。
   * @param url 
   * @param paramName 参数名称
   * @param value 参数值
   */
  private static _addParamForUrl(url: string, paramName: string,value :any){
    if(!url)return "";
    let index = url.indexOf(paramName + '=');  
    if (index > -1) {
      let oldValue = this.getQueryByName(url,paramName) 
      url = url.replace(paramName + '=' + oldValue, paramName + '=' + value ); //替换旧的value 
    }else {
      url += `&${paramName}=${value}`;
    }
    return url;
  }
  static addShareByUserToURL(link: string,userId: string ){
    if(!link)return ''
    if (link.indexOf('?') >= 0) {
      let queryValue = this.getQueryByName(link,'ShareByUser')
      if(!queryValue){
        link += "&ShareByUser=" + userId;
      }else {
        link = link.replace('ShareByUser=' + queryValue, 'ShareByUser=' + userId)
      } 
    } else {
      link += "?ShareByUser=" + userId;
    }
    return link;
  }
/**
 * 
 * @param url 获取某个url中某个参数的值
 * @param name 
 */
   static getQueryByName(url: string,name: string){ 
    var reg = new RegExp('[?&]'+name+'=([^&#]+)', "i");
    var query = url.match(reg);
    return query ? query[1] : null;
}

