  static isUrl(url: string) {
    const reg = /(http:\/\/|https:\/\/)((\w|=|\?|\.|\/|&|-)+)/g;
    let isurl = reg.test(url) 
    if(url) {
      url = url.trim()
    }
    let start = url.startsWith('http');
    return isurl && start;
  }
  static matchAppPrefix(url: string){
    const reg = /(iproud:\/\/)/ig;
    return reg.test(url)
  } 
  static parsePageUrl(url: string) {
    let res = {
      AppPageId: '',
      params: {}
    }
    if (!this.matchAppPrefix(url)) { 
      return res;
    }
    let newurl = url.replace(APPNAME_PAGE_PREFIX.toLowerCase(), '')
    let array = newurl.split('?')
    if (array[0]) {
      res.AppPageId = array[0].split('=')[1]
    }
    if (array[1]) {
      let param = {}
      let paramarray = array[1].split('&')
      for (let item of paramarray) {
        if (item) {
          param[item.split('=')[0]] = item.split('=')[1]
        }
      }
      res.params = param
    }
    return res
  }

