  static deepCopy(object) {
    if (object instanceof Array) {
      let newarray = []
      for (let item of object) {
        newarray.push(item)
      }
      return newarray
    } else if (object instanceof Object) {
      return JSON.parse(JSON.stringify(object));
    }
    return object;
  }

