  /**
   * 判断输入字符串的长度（中文占用两个字节，英文占用一个字节）
   * @param val :string
   */
  static getByteLen(val) {    //传入一个字符串
    var len = 0;

    for (var i = 0; i < val.length; i++) {
      let value = val[i];
      if (value.match(/[^\x00-\xff]/ig) != null) //全角 
        len += 2; //如果是全角，占用两个字节  如果mysql中某字段是text, 如果设置编码为utf-8,那么一个中文是占3个字节, gbk是两个字节
      else
        len += 1; //半角占用一个字节
    }
    return len;
  }


  static blurMobilePhone(phonemobile: string){
    if(!phonemobile)return '';
    let bit = 5;
    if(phonemobile.length > 0 && phonemobile.length <= 5)bit = 1
    if(phonemobile.length > 5 && phonemobile.length <= 8)bit = 2
    if(phonemobile.length > 8 && phonemobile.length <= 10)bit = 3
    if(phonemobile.length > 10)bit = 5
    let star = ''
    for(let i= 0; i< bit; i++){
      star += '*'
    } 
    return phonemobile.replace( phonemobile.substr(phonemobile.length - (bit + 3),bit),star)
  }
