  static captureWebPage(element: any): Observable<{ success: boolean, result: string, mills: number}>{
    let st = new Date().getTime()
    return new Observable(obersve=>{
      html2canvas(element )
      .then((canvas: any)=> { 
        let et = new Date().getTime()
        let src = canvas.toDataURL("image/png"); 
        obersve.next({ success: !!src,result: src ,mills : et - st}) 
      }).catch((error: any)=> {   
        obersve.error({ success: false,result: error})
      });
    })
    
  }

