  static _brightnessOfColor(colorHexdecimalValue: string){
    if(!colorHexdecimalValue)return 0;

    let rColorValue = 1;
    let gColorValue = 1;
    let bColorValue = 1;

    if(colorHexdecimalValue.indexOf('rgb') > -1){
      let rgb = colorHexdecimalValue.match(/(\d(\.\d+)?)+/g); 
      if(rgb){
        rColorValue = rgb[0] ? Number(rgb[0]) : 1;
        gColorValue = rgb[1] ? Number(rgb[1]) : 1;
        bColorValue = rgb[2] ? Number(rgb[2]) : 1;
      } 
    }else if(colorHexdecimalValue.indexOf('#') > -1){
      let useColorValue = colorHexdecimalValue.slice(1);
      let rColor = useColorValue.slice(0, 2);
      let gColor = useColorValue.slice(2, 4);
      let bColor = useColorValue.slice(4);

      rColorValue = this._hexToRgb(rColor);
      gColorValue = this._hexToRgb(gColor);
      bColorValue = this._hexToRgb(bColor);

    }else {
      return 0;
    } 
    let lightness = rColorValue * 0.299 + gColorValue * 0.587 + bColorValue * 0.114;
    return lightness;
  }

  static _hexToRgb(hexColor){
    return parseInt(hexColor, 16);
  }

 static decideTxtColor(colorHexdecimal){
    return this._brightnessOfColor(colorHexdecimal) < 128;
  }
 
  static base64ToArrayBuffer(base64) {
    var binary_string =  window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array( len );
    for (var i = 0; i < len; i++)        {
        bytes[i] = binary_string.charCodeAt(i);
    }
    return bytes.buffer;
}
