import { Session } from './../services/session';
import { STORAGE_KEY } from './../services/constants';
import { Utility } from './../services/utility';
import { Logger } from './../services/logger';
import { Observable } from 'rxjs';
import { NativeService } from './../services/native-service';
import { File } from '@ionic-native/file';
import { StorageService } from './../services/storage-service';
import { Events } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
/*
  Generated class for the CacheProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CacheProvider {

  ImgArray: any = [];
  cacheDirctory: string = ''//缓存路径
  cacheSize: number = 0;
  CountDown: Object;
  constructor(public http: HttpClient,
    public events: Events,
    public storageService: StorageService,
    public file: File,
    public nativeService: NativeService,
    public logger: Logger,
    public session: Session
  ) {
    this.CountDown = {}
  }

  async init() {

    this.cacheDirctory = this.file.dataDirectory + 'imageCache/';

    const ImgArray = await this.storageService.getItem('ImgUrlArray')
    if (ImgArray && this.ImgArray) this.ImgArray.concat(ImgArray);


    const cacheSize = await this.storageService.getItem(STORAGE_KEY.DATA.CACHE_SIZE)
    if (cacheSize && typeof (cacheSize) == "number") this.cacheSize += cacheSize;

  }

  reloadImage(url): Observable<any> {
    return Observable.create(async (observer) => { 
      if (!url) {
        observer.error(false);
        return
      }
      let fileName = url.split("/").pop();
      fileName = fileName.toLowerCase()
      if (fileName.lastIndexOf('.jpg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpg') + 4);
      } else if (fileName.lastIndexOf('.jpeg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpeg') + 5);
      } else if (fileName.lastIndexOf('.png') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.png') + 4);
      }

      try{
        const checkResult = await this.file.checkFile(this.cacheDirctory, fileName);
        if(checkResult) { 
          const removeResult = await this.file.removeFile(this.cacheDirctory, fileName)
          if(removeResult){
           
          }
        } 
      }catch(e){

      }finally{
        this.nativeService.download(url, this.cacheDirctory, fileName).subscribe() 
      }  
    })
  }

  getImagePath(url): Observable<any> {
    return Observable.create(observer => {
      if (!url) {
        observer.error(false);
        return
      }
      let fileName = url.split("/").pop();
      fileName = fileName.toLowerCase()
      if (fileName.lastIndexOf('.jpg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpg') + 4);
      } else if (fileName.lastIndexOf('.jpeg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpeg') + 5);
      } else if (fileName.lastIndexOf('.png') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.png') + 4);
      }

      this.nativeService.checkFile(this.cacheDirctory, fileName).subscribe(check => {
        if (check) {
          let itemSrc = this.cacheDirctory + fileName;
          if (this.nativeService.isIos()) {
            itemSrc = itemSrc.replace(/^file:\/\//, '');
          }
          observer.next(itemSrc);
        } else {
          this.nativeService.download(url, this.cacheDirctory, fileName).subscribe((res) => {
            this.addImageCache(res.fileName, res.fileSize);
          });
          observer.error(false);
        }
      })
    })
  }
  //将图片转成base64
  loadedImageOnBase64(url): Observable<any> {
    return Observable.create(observer => {
      if (!url) {
        observer.error(false);
        return
      }
      let fileName = url.split("/").pop();
      fileName = fileName.toLowerCase()
      if (fileName.lastIndexOf('.jpg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpg') + 4);
      } else if (fileName.lastIndexOf('.jpeg') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.jpeg') + 5);
      } else if (fileName.lastIndexOf('.png') > -1) {
        fileName = fileName.substring(0, fileName.lastIndexOf('.png') + 4);
      }

      this.nativeService.checkFile(this.cacheDirctory, fileName).subscribe(check => {
        if (check) {
          this.nativeService.convertImgToBase64(this.cacheDirctory + fileName).subscribe((result) => {
            observer.next(result);
          }, (error) => {
            this.nativeService.download(url, this.cacheDirctory, fileName).subscribe((res) => {
              this.addImageCache(res.fileName, res.fileSize);
            });
            observer.error(error);
          });
        } else {
          this.nativeService.download(url, this.cacheDirctory, fileName).subscribe((res) => {
            this.addImageCache(res.fileName, res.fileSize);
          });
          observer.error(false);
        }
      })
    })

  }

  //添加图片缓存
  addImageCache(fileName: String, cacheSize: number) {

    if (typeof (cacheSize) == "number") {
      this.cacheSize += cacheSize;
    }
    if (!this.ImgArray) this.ImgArray = [];
    if (fileName) this.ImgArray.push(fileName);

    this.storageService.setItem('ImgUrlArray', this.ImgArray);
    this.storageService.setItem(STORAGE_KEY.DATA.CACHE_SIZE, this.cacheSize);
  }

  //清除图片缓存
  clearImageCache() {

    this.file.removeRecursively(this.file.dataDirectory, 'imageCache').then((res) => {

      this.storageService.setItem(STORAGE_KEY.DATA.CACHE_SIZE, 0);
      //清除缓存图片
      this.storageService.setItem("ImgUrlArray", []);
      this.cacheSize = 0;
      this.ImgArray = [];
      this.events.publish('cache:clear-image');
    }).catch((err) => {
      this.logger.log(JSON.stringify(err), 'removeFile err');
    });

  }

  /**
   * 设置倒计时
   * @param key 倒计时key
   * @param countdown 倒计时时间（s）
   */
  setCountDown(key: string, countdown: number) {
    this.CountDown[key] = countdown
  }

  getCountDown(key: string, ) {
    return this.CountDown[key] || 0
  }

  /**
   * 识别二维码
   * @param imageUrl 图片路径或base64
   */
  decodeQRCode(imageUrl: string): Observable<string> {
    return Observable.create(observer => {
      // if(!Utility.isUrl(imageUrl)){
      //   observer.error(""); 
      // }
      //第一次解析二维码
      if (!this.nativeService.isMobile()) {
        observer.error('')
        return;
      };
      qrcode.decode(imageUrl)
      qrcode.callback = (firstDecodeResult) => {
        if (Utility.isUrl(firstDecodeResult)) {
          observer.next(firstDecodeResult)
        } else {
          if (!Utility.isUrl(imageUrl)) {
            this.nativeService.convertImgToBase64(imageUrl).subscribe((result) => {
              qrcode.decode(result)
              qrcode.callback = (secondDecodeResult) => {
                if (Utility.isUrl(secondDecodeResult)) {
                  observer.next(secondDecodeResult)
                } else {
                  observer.error("");
                }
              };
            }, (error) => {
              observer.error(error);
            });
          } else {
            //第一次解析失败,转成base64再解析一次
            this.loadedImageOnBase64(imageUrl).subscribe(base64 => {
              qrcode.decode(base64)
              qrcode.callback = (secondDecodeResult) => {
                if (Utility.isUrl(secondDecodeResult)) {
                  observer.next(secondDecodeResult)
                } else {
                  observer.error("");
                }
              };
            })
          }
        }
      }
    })
  }

  /**
   *  取得社群本地设置
   */
  getSocialSetting(): Promise<{ showSocial: boolean, showMass: boolean }> {
    let result = {
      showSocial: false,
      showMass: false
    }
    return new Promise(async (resolve, reject) => {
      try {
        const _enable_local_setting_social = await this.storageService.getItem(STORAGE_KEY.SETTING.ENABLE_LOCAL_SETTING_SOCIAL)
        if (_enable_local_setting_social) { // 启用了本地设置，从 storage 取值
          result.showMass = await this.storageService.getItem(STORAGE_KEY.SETTING.SHOWMASS)
          result.showSocial = await this.storageService.getItem(STORAGE_KEY.SETTING.SHOWSOCIAL)
        } else if (this.session.user) {
          result.showMass = Utility.boolean(this.session.user.showMass)
          result.showSocial = Utility.boolean(this.session.user.showSocial)
        } 
        resolve(result) 
      } catch (e) {
        reject(e)
      } 
    })

  }
}



