import { Events } from 'ionic-angular';
import { WidgetProvider } from './widget-provider';
import { ServerProvider } from './server-provider';
import { HttpService } from './../services/http-service';
import { Utility } from './../services/utility';
import { Session } from './../services/session';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Club } from '../models/club';
import { UserInfo } from '../models/userinfo';
import { Observable } from 'rxjs';
import { Webpage, ResponseObject } from '../models/models';
import { map } from 'rxjs/operators';


/*
  Generated class for the ClubProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ClubProvider {
  isDebug: boolean = false;
  baseURL: string = ''
  constructor(public http: HttpClient,
    public session: Session,
    public httpService: HttpService,
    public serverProvider: ServerProvider,
    public widgetProvider: WidgetProvider,
    public events: Events) {
    this.baseURL = this.isDebug ? 'http://localhost:6249/' : this.serverProvider.WebServer();
    (<any>window).ClubProvider = this
  }

  /**
   * 请求参数加上语言
   */
  baseParam(params?: any) {
    let base = {
      lang: this.session.language
    }

    if (params) {
      Object.assign(base, params)
    }
    return base
  }

  /**
   * 获取我的社团、交流园地
   * @param site 厂区
   * @param channelCode 频道
   */
  getMyClubs(site: string, channelCode: string) {
    let params = {
      "userId": this.session.userId,
      "channelCode": channelCode,
      "site": site
    }
    let seq = this.httpService.post('Community/UserBoards', true, this.baseParam(params), true).share();
    return seq;
  }

  /**
   * 
   * @param searchKey 搜索key
   */
  BoardSearch(searchKey: string) {
    let params = {
      "searchKey": searchKey
    }
    let seq = this.httpService.post('Community/BoardSearch', true, this.baseParam(params), true).share();
    return seq;
  }

  /**
   * 获取厂区
   */
  BoardSites() {
    let seq = this.httpService.post('Community/BoardSites', true, this.baseParam(), true).share();
    return seq;
  }

  /**
   * 获取推荐的社团
   * @param site 厂区
   */
  RecommendBoards(site: string) {
    let params = {
      "site": site,
      "channelCode": "Club"
    }
    Object.assign(params, this.baseParam(params))
    let seq = this.httpService.post('Community/RecommendBoards', true, this.baseParam(params)).share();
    return seq;
  }

  /**
   * 获取最近访问
   * @param site 厂区
   * @param take 数量
   */
  MyAccessBoards(site: string, take: number) {
    let params = {
      "userId": this.session.userId,
      "lang": this.session.language,
      "channelCode": "Club",
      "site": site,
      "take": take || 5
    }
    let seq = this.httpService.post('Community/MyAccessBoards', true, this.baseParam(params)).share();
    return seq;
  }

  /**
   * 关注社团
   * @param boardId 
   */
  FollowBoard(boardId: number) {
    let params = {
      "boardId": boardId
    }
    let seq = this.httpService.post('Community/FollowBoard', true, params).share();
    return seq;
  }

  /**
   * 搜索帖子
   * @param SearchKey 
   * @param Skip 
   * @param Take 
   */
  SearchAllPosts(SearchKey: string, Skip: number, Take: number) {
    let params = {
      "SearchKey": SearchKey,
      "Skip": Skip,
      "Take": Take
    }
    let seq = this.httpService.post('Community/SearchAllPosts', true, this.baseParam(params), true).share();
    return seq;
  }

  /**
   * 获取频道
   */
  getCommunityChannel() {
    let seq = this.httpService.post('Community/CommunityChannel', true, this.baseParam(), true).share();
    return seq;
  }

    /**
   * 获取社团首页内容
   */
  getCommunityContent(site: string, skip: number, take: number) {
    const seq = this.httpService.post('Community/GetHomePosts', true,{ site, skip, take}, false).pipe( map(x => {
      for (const item of x.result) {
        item.creationTime = Utility.formatSignTime(this.session.language, item.creationTime);
        let imgs = [];
        let thumbPictures = [];
        const result = [];
        if (item.pictures) {
          imgs = item.pictures ? item.pictures.split(';') : [];
        }
        if (item.thumbPictures) {
          thumbPictures = item.thumbPictures.split(';');
        }
        for (let i = 0; i < imgs.length; i++) {
          result.push({
            sourceUrl: imgs[i],
            id: i,
            pictureUrl: thumbPictures[i]
          });
        }
        item.thumbImgs = item.thumbPictures ? item.thumbPictures.split(';').slice(0, 3) : [];
        item.imgs = result;
      }
      x.isMore = x.result &&  x.result.length === take;
      return x;
    }));
    return seq;
  }


  /**
   * 我的关注
   */
  MyFollow() {
    return this.baseURL + 'Community/Mine/MyFollow'
  }

  /**
   * 我的发表
   */
  MyPublish() {
    return this.baseURL + 'Community/Mine/MyPublish'
  }

  /**
   * 我的评论
   */
  MyComment() {
    return this.baseURL + 'Community/Board/MyComment'
  }

  /**
   * 我的活动
   */
  MyActivity() {
    return this.baseURL + 'Community/Board/MyActivity'
  }

  /**
   * 我的收藏
   */
  MyFavorite() {
    return this.baseURL + 'Community/Mine/MyFavorite'
  }

  /**
   * 我的粉丝
   */
  MyFans() {
    return this.baseURL + 'Community/Mine/MyFans'
  }


  Activities(site: string) {
    return this.baseURL + 'Community/Board/Activities?site=' + site
  }

  /**
   * 社团详情
   * @param boardId 
   * @param channelCode 
   * @param site 
   */
  BoardDetail(boardId: number, channelCode: string, site: string) {
    // let context = "";
    // if(channelCode == "Club"){
    //   context = "Club"
    // }else{
    //   context = "Board"
    // }
    return `${this.baseURL}Community/${channelCode}/Index?boardId=${boardId}&channelCode=${channelCode}&site=${site}`
  }
  UserPage(userId: number) {
    return `${this.baseURL}Community/Member/MemberHomePage?memberId=${userId}`;
  }
  /**
   * 前往全部社团
   * @param site 厂区
   */
  AllBoards(site: string) {
    return this.baseURL + 'Community/Club/AllBoards?site=' + site
  }

  Topics(site: string) {
    return this.baseURL + 'Community/Board/Topics?site=' + site
  }

  PostDetail(postId: number) {
    return this.baseURL + 'Community/Post/PostDetail?postId=' + postId;
  }

  TopicDetail(topicId: number) {
    return this.baseURL + 'Community/Board/TopicDetail?topicId=' + topicId
  }

  ActivityDetail(activityId: number) {
    return this.baseURL + 'Community/Board/ActivityDetail?activityId=' + activityId
  }

  /**
   * 打开浏览器（社群功能）
   * @param url 
   * @param navCtrl 
   */
  openWebpage(url: string, navCtrl: any) {
    let page: Webpage = {
      url: url,
      options: {
        useNativeBrowser: 'iframe',
        canShare: false
      }
    }
    this.widgetProvider.openWebpage(page, navCtrl)
  }
}


