import { StorageService } from './../services/storage-service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpService } from '../services/http-service';
import { Session } from '../services/session';
import { ApproveTask } from '../models/workflow';
import { Certificate } from '../models/certificate';
/*
  Generated class for the WorkflowProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WorkflowProvider {
  cert: Certificate
  constructor(public http: HttpClient,
    public httpService: HttpService,
    public session: Session,
    public storage: StorageService) {
  }
  //取待辦任務
  getTodoTask(MobileTaskOnly: boolean) {
    let seq = this.httpService.post('MobileWF/TodoTasks', true, {
      "Lang": this.session.language,
      "MobileTaskOnly": MobileTaskOnly
    }, true).share();
    return seq;
  }

  //取已辦任務
  getFinishedTask(StartDate: string, EndDate: any) {
    let seq = this.httpService.post('MobileWF/FinishedTask', true, {
      "Lang": this.session.language,
      "StartDate": StartDate,
      "EndDate": EndDate
    }).share();
    return seq;
  }

  //取某Model下的簽核任務
  getTasksByModel(ModelName: string) {
    let seq = this.httpService.post('MobileWF/Tasks', true, {
      "Lang": this.session.language,
      "ModelName": ModelName,
    }).share();
    return seq;
  }

  //取得某條簽核任務的詳細信息
  getTaskDetails(PID: string, TaskID: string, ModelName: string) {
    let seq = this.httpService.post('MobileWF/TaskDeatils', true, {
      "lang": this.session.language,
      "pid": PID,
      "taskID": TaskID,
      "modelName": ModelName
    }).share();
    return seq;
  }

  //取我的單
  getMyProcess(StartDate: string, EndDate: any) {
    let seq = this.httpService.post('MobileWF/MyProcess', true, {
      "Lang": this.session.language,
      "StartDate": StartDate,
      "EndDate": EndDate
    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }

  //簽單
  doApproveTask(approveTask: ApproveTask) {
    let seq = this.httpService.post('MobileWF/ApproveProcessTask', true, Object.assign(approveTask, {
      "Lang": this.session.language,
      "UserCode": this.session.user.employeeInfo.staffNumber
    })).share();
    seq.subscribe((data: any) => {
    }, err => {

    });
    return seq;
  }

  //取代理設置
  getAgent() {
    let seq = this.httpService.post('MobileWF/AgentSetList', true, {
      "Lang": this.session.language
    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }

  //新增代理
  addAgent(UserCode: string, AgentCode: string, AgentType: string, StartDate: string, EndDate: string, Remark: string) {
    let seq = this.httpService.post('MobileWF/AddAgent', true, {
      "Lang": this.session.language,
      "UserCode": UserCode,
      "AgentCode": AgentCode,
      "AgentType": AgentType,
      "StartDate": StartDate,
      "EndDate": EndDate,
      "Remark": Remark,

    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }

  //刪除代理
  deleteAgent(AgentGuid: string) {
    let seq = this.httpService.post('MobileWF/DeleteAgent', true, {
      "Lang": this.session.language,
      "AgentGuid": AgentGuid,
    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }


  /**
   * 獲取历史代理人
   */
  AgentHisList() {
    let seq = this.httpService.post('MobileWF/AgentHisList', true, {
      "Lang": this.session.language,
    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }


  /**
   * 獲取历史代理人
   */
  UserName(userCode) {
    let seq = this.httpService.post('MobileWF/UserName', true, {
      "Lang": this.session.language,
      "userCode": userCode
    }).share();
    seq.subscribe((data: any) => {

    }, err => {

    });
    return seq;
  }


  getCertSubjectName(cert: Certificate): string {
    if(!cert.subject) {
      return ''
    }
    const res = cert.subject.find(x => x.shortName === 'CN')

    if(res && res.value) {
      return res.value
    } else {
      return ''
    }

  }

  /**
   * 获取证书信息
   */
  getCertification(): Promise<Certificate> {
    return new Promise(async (resolve, reject) => {
      this.cert = await this.storage.getItem(this.session.certKey);
      if (!this.cert) {
        reject({
          errcode: -1,
          msg: 'cert is invalid'
        })
        return;
      }

      // console.log(this.getCertSubjectName(this.cert), this.getCertSubjectName(this.cert) == '朱優良' , this.getCertSubjectName(this.cert) == this.session.user.employeeName) 
      if(!this.session.user || !this.session.user.employeeInfo) {
        reject({
          errcode: -5,
          msg: 'current user is null'
        })
        return;
      } else if(this.getCertSubjectName(this.cert) != this.session.user.employeeInfo.employeeName) {
        reject({
          errcode: -6,
          msg: 'cert user name is not match current user'
        })
        return;
      }

      if (!this.cert.password) {
        reject({
          errcode: -2,
          msg: 'password is empty'
        })
        return;
      }

      if (this.cert.validity && this.cert.validity.notAfter) {
        if (new Date(this.cert.validity.notAfter).getTime() < new Date().getTime()) {
          reject({
            errcode: -3,
            msg: 'certificate is invalid'
          })
          return;
        }
      }

      if (!this.cert.encryptData) {
        reject({
          errcode: -4,
          msg: 'file is invalid'
        })
        return;
      }
      resolve(this.cert);
    })
  }
   // E化联络单PDF（base64）
   GetProcessPDF(PID: number) {
    let seq = this.httpService.post('MobileWF/GetProcessPDF', true, {
      Lang: this.session.language,
      PID: PID
    }, true).share();
    return seq;
  }

  GetTmpProcessPDF(weburl: string) {
    let seq = this.httpService.get(weburl, true, {
      Lang: this.session.language,
      PID: weburl
    }, false).share();
    return seq;
  }

  
}



