
 
import { Injectable } from '@angular/core'; 
import { HttpService } from '../../services/http-service';
import { Session } from '../../services/session';


@Injectable()
export class SocialProvider {

  socialProfiles: any;
  constructor(private httpService: HttpService,
    private session: Session,  ) {

  }

  /**
   * 获取个人social信息
   */
  getProfiles() {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/Profiles', true, { "lang": this.session.language,TimeZone: TimeZone }, true).share();
    seq.subscribe((data: any) => { 
      if(data && data.result && data.result.length > 0){ 
        this.socialProfiles =  data.result[0]; 
      }
    });
    return seq;
  }

  /**
   * 签到
   * @param checkinDate 签到日期
   */
  CheckIn(checkinDate: string) {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/CheckIn', true, { Time: checkinDate ,TimeZone: TimeZone}).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }


  /**
   * 签到记录
   * @param Year 年份 
   * @param Month 
   */
  CheckInRecord(Year: string, Month: string) {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/CheckInRecord', true, { Year: Year, Month: Month,TimeZone: TimeZone}).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  CheckInRecord_New(Year: string, Month: string) {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('v2/Social/CheckInRecord', true, { Year: Year, Month: Month,TimeZone: TimeZone}).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  
  /**
   * 获取成就列表
   */
  getAchievements() {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/Achievements', true, { "lang": this.session.language,TimeZone: TimeZone },true).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  /**
   * 获取人任务清单
   */
  getTasks() {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/Tasks', true, { "lang": this.session.language,TimeZone: TimeZone }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  /**
   * 领取任务积分
   * TaskId不傳，領取所有任務積分
   * @param TaskId 任务id
   */
  TakeScore(TaskId?: string) {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let param = {TimeZone: TimeZone}
    if(TaskId){
      param['TaskId'] = TaskId
    }
    let seq = this.httpService.post('Social/TakeScore', true, param).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  /**
   * 员宝贝明细
   */
  CowryDetail() {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/CowryDetail', true, { "lang": this.session.language,TimeZone: TimeZone }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  
  /**
   * 歷史積分
   * @param skip 開始條數
   * @param Take 獲取條數
   */
  ScoreHis (skip: number = 1,Take: number = 10,isHideLoading: boolean = false) {
    let TimeZone = (-new Date().getTimezoneOffset()/60) 
    let seq = this.httpService.post('Social/ScoreHis', true, {"Lang":this.session.language,"Skip":skip,"Take":Take,TimeZone: TimeZone},isHideLoading).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }


  
}



