import { HandleProvider } from './../providers/handle/handle';
 

import {Injectable} from "@angular/core";
import {Events} from "ionic-angular";
import { JPush } from '@jiguang-ionic/jpush';
import {IS_DEBUG} from "./constants";
import { NativeService } from "../services/native-service";
import { HttpService} from "../services/http-service";
import { Session} from "../services/session";
/**
 * 极光推送
 */
@Injectable()
export class JPushService {

  count:number = 0;
  constructor(private jPush: JPush,
              private nativeService: NativeService,
              private httpService: HttpService,
              private events: Events,
              private session: Session,
              public handleProvider: HandleProvider) {
  }

  initJpush() {
    this.handleProvider.generate('JPushService:initJpush',0);
    if (!this.nativeService.isMobile()) {
      return;
    }
    
    this.jPush.init()
    this.jPush.setDebugMode(IS_DEBUG);
    this.getDeviceId(false);
    this.jPushAddEventListener();
  }

  private jPushAddEventListener() {
    this.jPush.getUserNotificationSettings().then(result => {
      if (result == 0) { 
      } else if (result > 0) { 
      }
    });
    //点击通知进入应用程序时会触发的事件
    document.addEventListener("jpush.openNotification", event => {
      this.handleProvider.generate('jpush.openNotification',0);
      let content = this.nativeService.isIos() ? event : event['extras']['cn.jpush.android.EXTRA'];
        this.events.publish('iProud.openNotification', content);
        this.handleProvider.generate('jpush:this.events.publish(iProud.openNotification, content);',0);
    }, false);
    //foreground收到通知时会触发该事件
    document.addEventListener("jpush.receiveNotification", event => {
      let extras = this.nativeService.isIos() ? event: event['extras']['cn.jpush.android.EXTRA'];
      this.events.publish('iProud.receiveNotification', extras);
    }, false);
    //background收到通知时会触发该事件jpush.backgroundNotification
    document.addEventListener("jpush.backgroundNotification", event => {
      let extras = this.nativeService.isIos() ? event: event['extras']['cn.jpush.android.EXTRA'];
      this.events.publish('iProud.receiveNotification', extras);
    }, false);

    //background收到customize message
    document.addEventListener("jpush.receiveMessage", event => {
      // alert(JSON.stringify(event));
      let extras = this.nativeService.isIos() ? event: event['extras']['cn.jpush.android.EXTRA'];
      this.events.publish('jpush.receiveCustomizeMessage', extras);
    }, false);
 
    //收到自定义消息时触发这个事件
    // document.addEventListener("jpush.receiveMessage", event => {
    //   let message = this.nativeService.isIos() ? event['content'] : event['message']; 
    // }, false);

  }

  
  setAlias() {
    if (!this.nativeService.isMobile()) {
      return;
    }
    this.jPush.setAlias({sequence: 1, alias: this.session.userId.toString()}).then((result) => { 
      console.log(result);
    }, (error) => {
      console.error('jpush-设置别名失败:' + error.code);
    });
  }

  deleteAlias() {
    if (!this.nativeService.isMobile()) {
      return;
    }
    this.jPush.deleteAlias({sequence: 2}).then((result) => {  
    }, (error) => {
      console.error('jpush-设删除别名失败:' + error.code);
    });
  }

  setTags(tags: Array<string> = []) {
    if (!this.nativeService.isMobile()) {
      return;
    }
    if (this.nativeService.isAndroid()) {
      tags.push('android');
    }
    if (this.nativeService.isIos()) {
      tags.push('ios');
    }
    this.jPush.setTags({sequence: 3, tags: tags}).then((result) => {  
    }, (error) => {
      console.error('jpush-设置标签失败:' + error.code);
    })
  }

  deleteTags(tags: Array<string> = []) {
    if (!this.nativeService.isMobile()) {
      return;
    }
    this.jPush.deleteTags({sequence: 4, tags: tags}).then((result) => {  
    }, (error) => {
      console.error('jpush-删除标签失败:' + error.code);
    })
  }

  /**
   * 清除所有通知
   */
  clearAllNotification(){
    this.jPush.clearAllNotification()
  }

  clearNotificationById(messageId){
    this.jPush.clearNotificationById(messageId);
  }
  //设置ios应用角标数量
  setIosIconBadgeNumber(badgeNumber) {
    if (this.nativeService.isIos()) {
      this.jPush.setBadge(badgeNumber);//上传badge值到jPush服务器
      this.jPush.setApplicationIconBadgeNumber(badgeNumber);//设置应用badge值
    }
  }
  //取得设备Id
  getDeviceId(isShowMsg) {  
    this.count ++;
    this.jPush.getRegistrationID().then((deviceId)=>{
      if(isShowMsg){
        this.nativeService.showToast("(count):" + this.count + ",DeviceId:" +deviceId);
      }
      this.session.deviceId = deviceId;
      if(deviceId!=null && deviceId!=undefined && deviceId != "") {
        if(this.session.userId!=null){
          this.httpService.post('User/DeviceId', true, {"deviceId": deviceId },true).subscribe((res)=>{ 
           })
        }
      }else if(this.count < 5){
        setTimeout(()=>{
          this.getDeviceId(isShowMsg)
        },3000)
      }
    });
  }  

}



