import { Observable } from 'rxjs';
import { DEFAULT_LOGO } from './../services/constants';
import { TranslateService } from '@ngx-translate/core';
import { Events } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Injectable,ViewChild } from '@angular/core';
import { DomSanitizer } from "@angular/platform-browser";
import {ToastController,Platform}from 'ionic-angular';
import { HttpService, Utility, Session, NativeService } from '../services/services';
import { Clipboard } from "@ionic-native/clipboard";

export interface ShareBody {
  message: {
    title: string
    description?: string
    thumb?: string
    media: object
  },
  scene: number //Wechat.Scene.TIMELINE 
}

export interface ShareMessage {
  title: string  //标题
  description?: string //描述
  thumb?: string; //缩略图
  resource: string //图片url或网页url
}

export interface Browser{
  isLoaded: false, // 网页是否被加载
  proObj: null, // 进度条对象
  progress: 0, // 网页访问的进度条
  secUrl: '', // 安全链接
  title: '加载中',
  originUrl: '', // 原始链接
  completeUrl: '', // 完整链接（添加了From, AuthCode, Lang）
  share: null,// 是否具有分享功能（传递一个分享对象ShareModel过来）
  description: ''
}
/*
  Generated class for the ShareProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ShareProvider {
  @ViewChild("iframe") _iframe;
  loadcount: number = 0;//加载次数
  constructor(public http: HttpClient,
    public nativeService: NativeService,
    public events: Events,
    public translate: TranslateService,
    private sanitizer: DomSanitizer,
    public httpService:HttpService,
    public session:Session,
    public platform:Platform,
    public toastCtrl:ToastController,
    public clipboard:Clipboard
  ) {
  }

  /**
   * 分享到微信
   * @param shareBody 分享body 
   */
  private _shareOnWechat(shareBody: ShareBody): Observable<any> {
    return Observable.create(observer => {
      if(this._isSupportWeChat()){
        this.nativeService.isInstallWeChat();
        shareBody.message.thumb = shareBody.message.thumb || DEFAULT_LOGO;
        shareBody.message.description = shareBody.message.description || ""; 
  
        Wechat.share(shareBody, (res) => {
          observer.next(res)
        }, (reason) => {
          observer.error(reason)
          console.error("Failed: " + JSON.stringify(reason));
        });
      }else{
        observer.next(true)
      } 
    })
  }


  // 分享网页到微信
  private _shareWebPageOnWx(messageBody: ShareMessage, scene: number) { 
    let url = Utility.removeSessionInfoForShareLink(messageBody.resource);
    url = Utility.addShareByUserToURL(url,this.session.userId)

    let msgBody = Object.assign(messageBody, { 
      media: {
        type: this._getTypeWebpage(),
        webpageUrl: url
      }
    })
    let shareBody: ShareBody = {
      message: msgBody,
      scene: scene
    }
    //console.log(shareBody)
    return this._shareOnWechat(shareBody)
  }

  // 分享图片到微信
  private _shareImageOnWx(messageBody: ShareMessage, scene: number) { 
    let msgBody = Object.assign(messageBody, {
      media: {
        type: this._getTypeImage(),
        image: messageBody.resource
      }
    })
    let shareBody: ShareBody = {
      message: msgBody,
      scene: scene
    }
    return this._shareOnWechat(shareBody)
  }

  // 分享网页微信朋友圈
  shareWebPageWxTimeLine(messageBody: ShareMessage) { 
    return this._shareWebPageOnWx(messageBody, this._getSceneTimeline())
  }

  // 分享网页到微信好友
  shareWebPageWxSession(messageBody: ShareMessage) {
    return this._shareWebPageOnWx(messageBody,this._getSceneSession())
  }

  // 分享图片微信朋友圈
  shareImageWxTimeLine(messageBody: ShareMessage) {
    return this._shareImageOnWx(messageBody, this._getSceneTimeline())
  }

  // 分享图片到微信好友
  shareImageWxSession(messageBody: ShareMessage) {
    return this._shareImageOnWx(messageBody, this._getSceneSession())
  }
   
  private _getSceneSession(): any{ 
    if(this._isSupportWeChat()){ 
      return Wechat.Scene.SESSION 
    }
    return 0
  }

  private _getSceneTimeline(): any{ 

    if(this._isSupportWeChat()){ 
      return Wechat.Scene.TIMELINE 
    }
    return 1
  }

  private _getTypeImage(): any{ 
    if(this._isSupportWeChat()){
      return Wechat.Type.IMAGE
    }
    return 7
  }

  private _getTypeWebpage(): any{
    if(this._isSupportWeChat()){
      return Wechat.Type.WEBPAGE
    }
    return 4
  }

  private _isSupportWeChat(): boolean{
    //console.log(typeof Wechat !== 'undefined')
    //console.log(Wechat)
    return this.nativeService.isMobile() && typeof Wechat !== 'undefined'
  }

}



