  /**
   * 更新聊天会话
   * @param messageSession 
   */
  updateMessageChat(messageSession: MessageSession): Observable<SqliteHandleResult> {
    return new Observable(observe => {
      if (!messageSession.id && messageSession.id != '0') {
        observe.error(false)
        console.error("insert into Messages failed,cause: ChatId is empty");
        return;
      } else if (!messageSession.userId) {
        observe.error(false)
        console.error("insert into Messages failed,cause: userID is empty");
        return;
      }
      let params = [];
      if (isNaN(Number(messageSession.latestTime))) {
        messageSession.latestTime = new Date(messageSession.latestTime).getTime().toString()
      }
      params.push(messageSession.latestTime)
      params.push(messageSession.isTop ? 1 : 0)
      params.push(JSON.stringify(messageSession))
      params.push(messageSession.id) //聊天室ID


      let sql = `update  ${this.table.messageChat} set latestTime = ?,  isTop = ?, Extra = ? where id = ? `;
      this.sqliteService.executeSql(sql, params).then((data) => {
        observe.next({
          result: true,
          message: 'update MessageChat Successfully!'
        })
      }).catch((err) => {
        observe.error({
          result: false,
          message: err
        })
      });
    })
  }

  /**
   * 插入聊天会话
   * @param messageSession 
   */
  insertMessageChat(messageSession: MessageSession): Observable<boolean> {
    return new Observable(observe => {
      if (!messageSession.userId) {
        observe.error(false)
        console.error("insert into Messages failed,cause: userID is empty");
        return;
      } else if (!messageSession.id && messageSession.id != '0') {
        observe.error(false)
        console.error("insert into Messages failed,cause: messageChatId is empty");
        return;
      }
      let params = [];
      if (isNaN(Number(messageSession.latestTime))) {
        messageSession.latestTime = new Date(messageSession.latestTime).getTime().toString()
      }
      params.push(messageSession.id ) //聊天室ID
      params.push(messageSession.id ) 
      params.push(messageSession.latestTime)
      params.push(messageSession.isTop ? 1 : 0)
      params.push(JSON.stringify(messageSession))

      let sql = `insert into ${this.table.messageChat} ( id, chatId, latestTime,istop, Extra) values(?,?,?,?,?) `;
      this.sqliteService.executeSql(sql, params).then((data) => {
        observe.next(true)
      }).catch((err) => {
        observe.error(false)
      });
    })
  }

  /**
   * 查询聊天会话
   * @param userId 用户id
   */
  queryMessageChat(): Observable<Array<any>> {
    return new Observable(observe => {
      let sql = `select chat.Extra,MAX(message.receiveTime), 
      message.extra as messageExtra from ${this.table.messageChat} chat 
      inner join ${this.table.message} message on message.chatid = chat.id 
      group by chat.chatid 
      order by isTop DESC,message.receiveTime DESC`

      // let sql = `SELECT * FROM  ${this.table.messageChat}  
      // order by isTop DESC,latestTime DESC  `;
      this.sqliteService.executeSql(sql, []).then((rst) => {
        let result = []
        for (let item of rst.res.data) {
          result.push(item)
        }
        observe.next(result)
      }).catch((err) => { 
        observe.error(err)
      });
    })
  }

