  updateMessageStatus(status: string, message: Message): Observable<SqliteHandleResult> { 
    return new Observable(observe => {
      if (!message.id && message.id != 0) {
        observe.error(false)
        console.error("update into Messages failed,cause: messageID is empty");
        return;
      }else if(!status){
        observe.error(false)
        console.error("status is empty");
        return;
      } 
      message.status = status; 
      let params = []; 
      params.push(JSON.stringify(message))  
      params.push(message.id) 
      let sql = `update ${this.table.message} set Extra = ? where id = ?  `;
      this.sqliteService.executeSql(sql, params).then((data) => {
        observe.next({result: true,message: 'update message ok!'})
      }).catch((err) => {
        observe.error({result: false,message: err})
      });
    }) 
  }

  updateMessage( message: Message): Observable<SqliteHandleResult> { 
    return new Observable(observe => {
      if (!message.id && message.id != 0) {
        observe.error(false)
        console.error("update into Messages failed,cause: messageID is empty");
        return;
      }   
      let params = []; 
 
      params.push(Number(message.receiveTime)) 
      params.push(JSON.stringify(message))  
      params.push(message.id) 
      let sql = `update ${this.table.message} set receiveTime = ? ,Extra = ? where id = ?  `;
      this.sqliteService.executeSql(sql, params).then((data) => {
        observe.next({result: true,message: 'update message ok!'})
      }).catch((err) => {
        observe.error({result: false,message: err})
      });
    }) 
  }
  getTables(){
    let keys = Object.keys(this.table) 
    let res = [];
    for(let item of keys){
      let obj = {
        name: item,
        value :this.table[item],
        checked: false
      }
      res.push(obj)
    }
    return res;
  }
