  /**
   * 插入多笔记录，先删除记录
   * @param friends friend数组
   */
  saveContacts(friends: Array<Friends>, GroupId?: number | string): Observable< SqliteHandleResult >{
    return new Observable(observe => {
      let querysql = `delete from  ${this.table.friends}`  
      let queryParam = []
      if(GroupId || GroupId === 0 || GroupId === '0') {
        querysql += " where GroupId = ?";
        queryParam.push(GroupId)
      }
      this.sqliteService.executeSql(querysql, queryParam).then((data) => {

        if(friends.length == 0){
          observe.next({
            result: true,
            message: 'insert contacts ok'
          })
        }else{
          let params = [];
          let valuesStr = ''
          for(let  friend of friends ){ 
            params.push(friend.FriendId ) 
            params.push(friend.GroupId) 
            params.push(friend.IsStar ? 1 : 0) 
            params.push(JSON.stringify(friend.Extra))
            valuesStr ? valuesStr += ',(?,?,?,?)' : valuesStr += '(?,?,?,?)' 
          }  
          let sql = `INSERT INTO ${this.table.friends} (FriendId,GroupId,IsStar,Extra) VALUES${valuesStr} `;
          this.sqliteService.executeSql(sql, params).then((data) => {
            observe.next({
              result: true,
              message: 'insert contacts ok'
            })
          }).catch((err) => {
            observe.error({
              result: true,
              message: err
            }) 
        }); 
        }
        
      }).catch((err) => {
        observe.error({
          result: true,
          message: err
        })
      });
    })
  }

 
