import { JPushService } from './../services/jpush-service';
import { MessageProvider } from './message-provider';
import { SqliteProvider } from './sqlite-provider';
import { UserProvider } from './user-provider';
import { HandleProvider } from './handle/handle';
import { WidgetProvider } from './widget-provider';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { Session } from '../services/session';
import { Observable } from 'rxjs';
import { HOT_CONTENT_URL } from '../services/constants';
import { Utility } from '../services/utility';
import { ServerProvider } from './server-provider';
import { Injectable } from "@angular/core"; 
import { Logger } from "../services/logger";
import { HttpService } from "../services/http-service";
import { NativeService } from "../services/native-service"; 
import { StorageService } from "../services/storage-service";
import { APPLE_STORE_APPID } from "../services/constants";
import { Events } from "ionic-angular";
import { TranslateService } from '@ngx-translate/core';
import { Deeplinks } from '@ionic-native/deeplinks'; 
import { Webpage } from '../models/models';

@Injectable()
export class ConfigProvider {
  appName;//如app id为com.kit.ionic2tabs,则appName为ionic2tabs
  appType;//android 或 ios
  currentVersionNo;//当前版本号
  latestVersionNo;//最新版本号
  lastVersionInfo;//从后台获取到的app最新版本信息
  versions;//app更新日志
  isMobile = false;//是否移动端
  appDownloadPageUrl;//下载页访问地址
  apkUrl;//android apk地址
  //app更新进度.默认为0,在app升级过程中会改变
  updateProgress: number = -1;
  eventsSet: Map< string, Array<string>>;
  language: string = 'us';
  isInit: boolean = true; //是否正在初始化
  isShowUpgraderPage: boolean = false;
  canUpgrade: boolean = false;
  isIgnore: boolean = false;
  constructor(public nativeService: NativeService, 
    public httpService: HttpService, 
    public storageService: StorageService,
    public logger: Logger,
    public serverProvider: ServerProvider, 
    public events: Events,
    public session: Session,
    public translate: TranslateService,  
    public http: HttpClient, 
    private deeplinks: Deeplinks,
    private widgetProvider: WidgetProvider,
    private handleProvider: HandleProvider,
    public userProvider: UserProvider,
    public sqlprovider: SqliteProvider,
    public messageProvider: MessageProvider,
    public jpushService: JPushService) { 
      this.eventsSet = new Map([]);
  }

  /**
   * 初始化VersionProvider，获取系统语言；获取本地版本；获取最新版本；
   */
  async init() {
    try {
      const lang = await this.storageService.getItem("language");
      switch (lang) {
        case 'zh-CN': this.language = 'cn'; break;
        case 'zh-TW': this.language = 'tw'; break;
        default: this.language = 'us';
      }

      this.isMobile = this.nativeService.isMobile();
      if (this.isMobile) {
        this.nativeService.getVersionNumber().subscribe(currentVersionNo => {
          this.currentVersionNo = currentVersionNo;
          this.storageService.setItem("version", this.currentVersionNo);
        });
        this.getLastestVersion(this.language).subscribe((data) => {
          //this.upgradeHint(data.latestVersionNo);
        }, err => {
          this.logger.log(JSON.stringify(err), "VersionProvider.getLastestVersion");
        })
      } else {
        this.currentVersionNo = '1.12';
        this.storageService.setItem("version", '1.12');
        this.getLastestVersion(this.language).subscribe((data) => {
          //this.upgradeHint(data.latestVersionNo);
        }, err => {
          this.logger.log(JSON.stringify(err), "VersionProvider.getLastestVersion");
        })
      }
    } catch (err) {
      this.logger.log(JSON.stringify(err), "this.storageService.getItem(\"language\")");
    }

  };

  /**
   * ANDROID：获取后台版本
   * IOS：获取APP STORE版本
   * @param language 语言
   */
  getLastestVersion(language): Observable<any> {
    let isEmulateIOS = true;
    return Observable.create(observer => {
      if (this.nativeService.isAndroid()) { //安卓
        let seq = this.httpService.post('Version/LatestVersion', true,{
            "versionType": 'Android',
            "Lang": language
          }, true).share().subscribe((data: any) => {
            if (data.errcode == 0) {
              
              this.latestVersionNo = data.result.versionNumber;
              this.lastVersionInfo = data.result;
              this.lastVersionInfo.releaseNotes = data.result.description;
              this.appDownloadPageUrl = this.serverProvider.AppDownloadPage();
              observer.next(this.fillData())
            }
          }, err => {
            observer.error(err);
            this.logger.log(JSON.stringify(err), "Version/LatestVersion");
          });
      } else if (this.nativeService.isIos()) {
        //调用苹果提供的API获取版本号
        let url = `https://itunes.apple.com/${language}/lookup?id=${this.serverProvider.AppStoreId()}`;
        let seq = this.httpService.post(url, true, { "id": APPLE_STORE_APPID }, true).share();
        seq.subscribe((data: any) => { 
          this.fillValue(data);
          observer.next(this.fillData())
        }, err => {
          this.logger.log(JSON.stringify(err), `https://itunes.apple.com/${language}/lookup?id=${this.serverProvider.AppStoreId()}`);
        });
      } else if (isEmulateIOS) {
        //调用苹果提供的API获取版本号
        let url = `https://itunes.apple.com/${language}/lookup?id=${this.serverProvider.AppStoreId()}`;
        let seq = this.httpService.post(url, true, { "id": APPLE_STORE_APPID }, true).share();
        seq.subscribe((data: any) => { 
          this.fillValue(data);
          observer.next(this.fillData())
        }, err => {
          this.logger.log(JSON.stringify(err), `https://itunes.apple.com/${language}/lookup?id=${this.serverProvider.AppStoreId()}`);
        });
      } else {
        this.httpService.post('Version/LatestVersion', true, {
          "versionType": 'Android',
          "Lang": language
        }, true).share().subscribe((data: any) => {
          if (data.errcode == 0) {
            this.latestVersionNo = data.result.versionNumber;
            this.lastVersionInfo = data.result;
            this.lastVersionInfo.releaseNotes = data.result.description;
            this.appDownloadPageUrl = this.serverProvider.AppDownloadPage();
            observer.next(this.fillData())
          }
        }, err => { 
        });
      }
    });
  }

  fillData() {
    this.events.publish('version:got-globalSetting');
    return {
      latestVersionNo: this.latestVersionNo,
      lastVersionInfo: this.lastVersionInfo,
      appDownloadPageUrl: this.appDownloadPageUrl
    }
  }
  fillValue(data: any) {

    if (data.errcode == 0 && data.result.resultCount > 0) {

      this.latestVersionNo = data.result.results[0].version;
      this.appDownloadPageUrl = data.result.results[0].trackViewUrl;
      this.lastVersionInfo = data.result.results[0]; 
    }
  }

  /**
   * 更新提示，可忽略
   * @param versionNumber 最新版本号
   */
  upgradeHint(versionNumber) { 
    this.storageService.getItem("isIgnore" + versionNumber).then(isIgnore => {
      if (!isIgnore && Utility.checkNewVersion(versionNumber, this.currentVersionNo)) {
        
        this.canUpgrade = true;
        this.events.publish('version:upgrade-available', () => { })
      }
    });
  }

  getSeverVersion() {
    return this.httpService.get(HOT_CONTENT_URL.FILE_URL, false, null, true);
  }

  /**
   * 获取全局设置中android upgraderUrl
   */
  getGlobalSettings() {
    let seq = this.httpService.post('Config/GlobleSettings', true, {
      "Lang":this.session.language,
      "type":"frontend" }, true).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
      }
    }, err => {
      console.error('Config/GlobleSettings', JSON.stringify(err))
    });

    return seq;
  }
    
  getCurrentVersionNo() {
    return this.currentVersionNo;
  }

  getLatestVersionNo() {
    return this.latestVersionNo;
  }

  getLastVersionInfo() {
    return this.lastVersionInfo;
  }
 

    /**
   * 获取多语言文本
   */
  getLangContext(options = {}) { 
    let defaultOption = { "Lang":this.session.language  }
    Object.assign(defaultOption,options)

    let seq = this.httpService.post('Config/LangContext', true, defaultOption, true).share();
    seq.subscribe((data: any) => {
      if (data.errcode == 0) {
        this.session.LangContexts = [] 
        for(let item of data.result){
          if(item.fieldName.toLowerCase() == 'iproudname' || item.fieldName.toLowerCase() == 'widgetname'){
            if(item.keyId == 1)item.keyId = 0
            this.session.LangContexts.push(item);
          }
        } 
        this.events.publish('refreshSessionName:LangChanged');
      }
    }, err => {
      console.error('Config/LangContext', JSON.stringify(err))
    });

    return seq;
  } 


  /**
   * 收集用户行为
   * @param PageName 页面名称
   * @param Action 动作
   * @param Data 数据
   */
  Behavior(PageName: string,Action: string,Data: any) { 
    let defaultOption =  {
      "UserId": this.session.userId,
      "PageName": PageName,
      "Action":Action,
      "Data":Data} 

    let seq = this.httpService.post('Config/Behavior', true, defaultOption, true).share();
    seq.subscribe((data: any) => { 
    }, err => {
      console.error('Config/LangContext', JSON.stringify(err))
    }); 
    return seq;
  } 

  
  /**
   * 更改语言设置
   * @param lang 
   */
  changeLanguage(lang: string,isNotRefresh: boolean){
    
    this.userProvider.privateSetting([{
      "name": "Lang",
      "value": lang
    }]);
    this.session.language = lang;
    this.storageService.setItem("language",lang);  
    this.translate.currentLoader = new TranslateHttpLoader(this.http, './assets/i18n/', '.json');
    this.translate.use(lang);
    this.events.publish('lang:changed',{isNotRefresh: isNotRefresh ,language:lang}); //需要刷新主页语言
    this.getLangContext().subscribe()
  }

  AfterNewEmployeeBind(inCompanyDateStr: string,siteCode: string):Observable <string>{
    return new Observable((observe)=>{
      if(inCompanyDateStr){
        let inCompanyDate = new Date(inCompanyDateStr).getTime();
        let today = new Date().getTime();
        let expireMills = 3 * 24 *60 *60 * 1000;
        // this.storageService.getItem('newEmployeeNotice').then(res=>{
          if(inCompanyDate + expireMills > today ) { 
          /**判断session中是否已经获取到globalSetting，若没有，则从服务器中获取后再发送新员工指示。 */
          if(!this.session.globalSetting){
            this.getGlobalSettings().subscribe(res => {
              this.session.globalSetting = res.result; 
              let result = this._sendNewEmployeeNotice(res.result.siteCode)
              observe.next(result ? 'Send message successfully!' : 'No entry guidance in the site, please contact the administrator to add!')
            })
          }else{
            let result = this._sendNewEmployeeNotice(siteCode);
            observe.next(result ? 'Send message successfully!' : 'No entry guidance in the site, please contact the administrator to add!')
          } 
        }else{
          observe.next('Not NewEmployee!')
        }
        // })
        
      }else {
        observe.next('inCompanyDate is Empty!')
      }
    }) 
  }
   
  private _sendNewEmployeeNotice(siteCode: string): boolean{
    if(!siteCode)return false;
    for(let item of this.session.globalSetting){   
      if(item.name.toLowerCase() == ('newemp-' + siteCode.toLowerCase())){
        let value:any = {};
        value['imageUrl'] = item.value 
        let extras = {
          widgetId: value.widgetId || 0,
          messageId: value.messageId || 'newEmployee:' + new Date().getTime(),
          widgetName: value.widgetName || 'iProud',  
          formatType: value.formatType || 'image',
          imageUrl: value.imageUrl,
          redirectUrl: value.redirectUrl,
          groudId: value.groudId , //群组id
          senderId: value.senderId, //发送人id
          senderName: value.senderName ,//发送人名称
          senderAvator : value.senderAvator ,//发送人头像
          fromType: value.fromType || 'AdminConsole' ,//发送来源类型 
        }
        this.events.publish('iProud.receiveNotification', extras );
        // this.nativeService.showToast(this.translate.instant('sd') + 'test')
        this.storageService.setItem('newEmployeeNotice',true);
        return true
      } 
    }
    return false;
  }

  ConfigDeeplinks(nav: any){
    this.deeplinks.route({
      '/about-us/:userid': 'AboutPage'  ,
      '/webview/:url': 'webview'
    }).subscribe(match => {
      // match.$route - the route we matched, which is the matched entry from the arguments to route()
      // match.$args - the args passed in the link
      // match.$link - the full link data
      this.handleProvider.generate('deeplink match',-1, match);
      let route = match.$route;
      let args = match.$args;
      let link = match.$link;
      if('webview' == route){
        let page: Webpage={
          url: args.url,
          options: {
            allowCopyLink: true,
            allowOpenInBrowser: true,
            canShare: true,
            useNativeBrowser: ''
          }
        }
        this.widgetProvider.openWebpage(page,nav);
      }else{
        nav.push(route,args)
      }  
    }, nomatch => {
      // nomatch.$link - the full link data 
      this.handleProvider.generate('deeplink no match',-1, nomatch);
    });
  } 

  initDatabase(){
    if(this.sqlprovider.isInit)return
    this.sqlprovider.init(this.session.userId).subscribe(res=>{ 
      this.events.publish('database:initialization') //初始成功后发布事件
      this.messageProvider.init().then(() => {//初始化后消息provider后再进行极光推送初始化，否则有可能出现极光接收到消息而没保存在provider里
        this.jpushService.initJpush();//初始化极光推送 
        this.messageProvider.getMessageMissing().subscribe(res=>{ //获取丢失消息
          if(res.errcode == 0 && res.result){
            let i = 0;
            for(let extra of res.result ){
              setTimeout(()=>{
                this.events.publish('iProud.receiveNotification', extra)
              }, i * 100) 
            } 
            i++;
          }
        })
      }); 
    },err=>{
      this.handleProvider.generate('init', -1, err)
    });
  }

  subscribeEvent(handler: any, handleId: string, array: Array<string>, callback: { func: Function, params: any }) {
    if(!handleId)  return 
    if(array && array.length > 0){
      array.forEach(eventName => { 
        let canAddEvent = this.addEvents(handleId,eventName);
        if(canAddEvent){
          handler.events.subscribe(eventName, () => {
            callback && callback.func(callback.params);
          });
        } 
      }); 
    } 
  }

  addEvents(handleId: string, eventName: string): boolean{
    let eventlist = this.eventsSet.get(handleId) || []
    if(eventlist.findIndex(p => p == eventName) == -1){
      eventlist.push(eventName);
      this.eventsSet.set(handleId, eventlist)
      return true
    } 
    return false
  }
}


