import { Widget } from './../models/widget';
import { ServerProvider } from './server-provider';
import { HandleProvider } from './handle/handle';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { DEFAULT_LOGO, OPEN_WIDGET_WAY } from './../services/constants';

import { Logger } from './../services/logger';
import { TranslateService } from '@ngx-translate/core';
import { Clipboard } from '@ionic-native/clipboard';
import { StorageService } from './../services/storage-service';
import { NativeService } from './../services/native-service';
import { Events, AlertController } from 'ionic-angular';
import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';
import { HttpService } from '../services/http-service';
import { Session } from '../services/session';
import { Utility } from '../services/utility';
import { NavController } from 'ionic-angular';
import { Webpage } from '../models/models';
import { ThemeableBrowser, ThemeableBrowserObject, ThemeableBrowserOptions } from '@ionic-native/themeable-browser';
import { ShareProvider, ShareMessage } from './share-provider';

@Injectable()
export class WidgetProvider {
  _user: any;
  browser: ThemeableBrowserObject;
  innerBrowser: Boolean;
  openWay: string;
  share: any;

  page: Webpage;
  browserMenuEvents = {
    shareFriends: 'shareFriends', //分享给微信朋友
    shareMoment: 'shareMoment',  //分享到微信朋友圈
    openInBrowser: 'openInBrowser', //在浏览器中打开
    copyUrl: 'copyUrl', //复制链接
    Refresh: 'Refresh' //刷新
  }



  openBrowserTarget: string = '_blank'; //打开浏览器方式
  options: ThemeableBrowserOptions;
  constructor(private httpService: HttpService,
    private session: Session,
    public themeableBrowser: ThemeableBrowser,
    public events: Events,
    public nativeService: NativeService,
    public storageService: StorageService,
    private translate: TranslateService,
    private clipboard: Clipboard,
    public logger: Logger,
    public shareProvider: ShareProvider,
    public iab: InAppBrowser,
    public handler: HandleProvider,
    public serverProvider: ServerProvider,
    public alertCtrl: AlertController ) {

    this.getConfig()

  }

  async getConfig() {
    this.innerBrowser = await this.storageService.getItem(this.session.userId + "browserCore");
    this.openWay = await this.storageService.getItem(this.session.userId + "openWay");
    this.events.subscribe('change:openWay', (innerBrowser, openWay) => {
      this.innerBrowser = innerBrowser;
      this.openWay = openWay;
    })
  }
  //"iproud://AppPageId=WorkflowDetailsPage?modelname=HandSignLF_New&pid=19697185&taskid=167&app_needSign=false&from=message" 
  // 開啓WebApp 或 WebPage的公共方法
  openWebpage(page: Webpage, navCtrl: NavController, isAddAccountId = false, callback?: any) {
    // page.url =  "AppPageId=WorkflowDetailsPage?modelname=ACKN2_PO_V4&pid=18863436&taskid=3&app_needSign=true"
    // page.url =  "iproud://AppPageId=WorkflowDetailsPage?modelname=HandSignLF_New&pid=19697185&taskid=167&app_needSign=false&from=message" 
    if (!page.url) return;

    if(page.url ){ // 判断是否为community
      let tmpurl = page.url
      tmpurl = tmpurl.split('?')[0]
      if(tmpurl.indexOf('iproud.fihmb.com') > -1 && tmpurl.indexOf('Community') > -1){
        page.options.useNativeBrowser = 'iframe'
        page.options.canShare = false
      }
    }
    
    this.page = page;
    if (!Utility.isUrl(page.url)) { // 是否为url
      let pageItem = Utility.parsePageUrl(page.url)
      try {
        if (pageItem.AppPageId) {
          navCtrl.push(pageItem.AppPageId, pageItem.params)
        } else {
          navCtrl.push(page.url, page)
        }
      } catch (error) {
        JSON.stringify(error)
      }
      return
    }

    /**
     * AppPageId  頁面id(WorkflowDetailsPage)
     * modelname modelname
     * pid pid
     * taskid taskid
     * from 來源(推送: 'message')
     */
    // let str = `iproud://AppPageId=${AppPageId}?modelname=${modelname}&pid=${pid}&taskid=${taskid}&from=${message}"`
    this.getAuthCode(page.appId,page.url).subscribe((data: any) => {
    // this.httpService.post('v2/Auth/GetCode', true, { AppId: page.appId || 0 }).subscribe((data: any) => {
      try {
          
      
        if (data.errcode == 0) {
          
          var code = data.result.code;
          let completeUrl = Utility.completeUrlForBrowser(page.url, code, this.session, page.appId > '0' ? page.appId.toString() : null, isAddAccountId);

          this.InAppBrowserOptions(page);

          let isShare = Utility.getBoolean(page.options.canShare, true)   //默认为 true
          let allowCopyLink = Utility.getBoolean(page.options.allowCopyLink, true)  //默认为 true
          let allowOpenInBrowser = Utility.getBoolean(page.options.allowOpenInBrowser, true) //默认为 true
          let useNativeBrowser = page.options.useNativeBrowser || OPEN_WIDGET_WAY.INAPPBROWSER//默认为 true

          /** 是否能够分享 */
          if (isShare) {
            this.share = {
              title: page.title, // 标题
              description: page.description, // 描述
              thumb: page.imageUrl, // 显示的缩略图
              url: completeUrl
            }
            this.options.menu.items.push({
              event: this.browserMenuEvents.shareFriends,
              label: this.translate.instant('BROWSER_POPOVER_WECHAT')
            })
            this.options.menu.items.push({
              event: this.browserMenuEvents.shareMoment,
              label: this.translate.instant('BROWSER_POPOVER_WECHAT_MOMENT')
            })
          } else {
            this.share = null;
          }

          /** 是否能复制链接 */
          if (allowCopyLink) {
            this.options.menu.items.push({
              event: this.browserMenuEvents.copyUrl,
              label: this.translate.instant('BROWSER_POPOVER_COPYURL'),
              toast: this.translate.instant('BROWSER_COPIED')
            })
          }
          /** 是否能在浏览器打开 */
          if (allowOpenInBrowser) {
            this.options.menu.items.push({
              event: this.browserMenuEvents.openInBrowser,
              label: this.translate.instant('BROWSER_POPOVER_OPENINBROWSER')
            })
          }

          this.options.menu.items.push({
            event: this.browserMenuEvents.Refresh,
            label: this.translate.instant('BROWSER_POPOVER_REFRESH')
          })
          if (this.nativeService.isIos()) {
            this.options.title.fontSize = 19;
          }

          let params = {
            browser: {
              appId: page.appId,
              title: page.title,
              originUrl: page.url,
              completeUrl: completeUrl,
              share: this.share,
              description: page.description,
              options: {
                allowCopyLink: allowCopyLink,
                allowOpenInBrowser: allowOpenInBrowser
              }
            },
            callback: callback
          }
          if (this.innerBrowser) { //高级配置，自定义打开方式
            this.openByBrowser(this.openWay, completeUrl, navCtrl, params);
          } else { //系统配置
            if (useNativeBrowser && this.nativeService.isMobile()) {
              this.openByBrowser(useNativeBrowser, completeUrl, navCtrl, params)
            } else {
              navCtrl.push('BrowserPage', params, { updateUrl: true });
            }
          }
        }
      } catch(e) {
        alert(JSON.stringify(e))
      }

    });
  }

  openByBrowser(browserName: string, completeUrl: string, navCtrl: any, params: any) {
    browserName = browserName ? browserName.toLowerCase() : null;
    if(completeUrl){
      completeUrl = completeUrl.trim()
    }
    switch (browserName) {
      case OPEN_WIDGET_WAY.INAPPBROWSER.toLowerCase():
        this.runForInAppBrowser(completeUrl);
        break;
      case OPEN_WIDGET_WAY.IFRAME.toLowerCase():
        navCtrl.push('BrowserPage', params, { updateUrl: true });
        break;
      case OPEN_WIDGET_WAY.LOCALBROWSER.toLowerCase():
        this.openOnSystem(completeUrl)
        break;
      case OPEN_WIDGET_WAY.ASK.toLowerCase():
        this.openOptions((browserName) => {
          this.openByBrowser(browserName, completeUrl, navCtrl, params);
        })
        break;
      default:
        navCtrl.push('BrowserPage', params, { updateUrl: true });
    }
  }

  openOptions(callback) {
    let alert = this.alertCtrl.create({
      title: 'open browser way',
      inputs: [
        {
          name: OPEN_WIDGET_WAY.IFRAME,
          type: 'radio',
          label: OPEN_WIDGET_WAY.IFRAME,
          value: OPEN_WIDGET_WAY.IFRAME
        },
        {
          name: OPEN_WIDGET_WAY.INAPPBROWSER,
          type: 'radio',
          label: OPEN_WIDGET_WAY.INAPPBROWSER,
          value: OPEN_WIDGET_WAY.INAPPBROWSER
        },
        {
          name: OPEN_WIDGET_WAY.LOCALBROWSER,
          type: 'radio',
          label: OPEN_WIDGET_WAY.LOCALBROWSER,
          value: OPEN_WIDGET_WAY.LOCALBROWSER
        }
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: data => {
            callback()
          }
        },
        {
          text: 'OK',
          handler: data => {
            callback(data)
          }
        }
      ]
    });
    alert.present();
  }

  openOnSystem(completeUrl:string) {
    this.iab.create(completeUrl, '_system');
  }
  openWebpageByIFrame(page: Webpage, navCtrl: NavController, isShare: boolean = true, callback?: any) {
    if (!page.url) return;

    this.page = page;
    if (!Utility.isUrl(page.url)) {
      let pageItem = Utility.parsePageUrl(page.url)
      try {
        if (pageItem.AppPageId) {
          navCtrl.push(pageItem.AppPageId, pageItem.params)
        } else {
          navCtrl.push(page.url, page)
        }
      } catch (error) {
        JSON.stringify(error)
      }
      return
    }
    this.getAuthCode(page.appId, page.url).subscribe((data: any) => {
    // this.httpService.post('v2/Auth/GetCode', true, { AppId: page.appId || 0 }).subscribe((data: any) => {
      if (data.errcode == 0) {
        var code = data.result.code;
        let completeUrl = Utility.completeUrlForBrowser(page.url, code, this.session, page.appId > '0' ? page.appId.toString() : null);

        if (isShare) {
          this.share = {
            title: page.title, // 标题
            description: page.description, // 描述
            thumb: page.imageUrl, // 显示的缩略图
            url: completeUrl
          }
        } else {
          this.share = null;
        }
        navCtrl.push('BrowserPage', {
          browser: {
            appId: page.appId,
            title: page.title,
            originUrl: page.url,
            completeUrl: completeUrl,
            share: this.share,
            description: page.description,
            options: {

            }
          },
          callback: callback
        }, { updateUrl: true });
      }

    });
  }

  buildShareMessage(res): ShareMessage {
    let messageBody: ShareMessage = {
      title: res.title || this.share.title,
      description: this.share.description,
      thumb: this.share.thumb || DEFAULT_LOGO,
      resource: res.url
    };

    return messageBody;
  }
  // 分享微信朋友圈
  shareWxTimeLine(res) {
    this.shareProvider.shareWebPageWxTimeLine(this.buildShareMessage(res)).subscribe(() => {
      // this.browser.show()
    })
  }

  // 分享到微信好友
  shareWxSession(res) {
    this.shareProvider.shareWebPageWxSession(this.buildShareMessage(res)).subscribe(() => {
      // this.browser.show()
    })
  }



  runForInAppBrowser(completeUrl) {

    // this.handler.generate("completeUrl",-1, {
    //   completeUrl:completeUrl,
    //   openBrowserTarget: this.openBrowserTarget,
    //   options: this.options
    // })
    // if (this.browser) {
    //   this.browser = null;
    // }
    this.browser = this.themeableBrowser.create(completeUrl, this.openBrowserTarget, this.options)
    this.browser.on(this.browserMenuEvents.shareFriends).subscribe((res) => {
      this.shareWxSession(res)

    }, (err) => {
      alert(JSON.stringify(err))
      this.logger.log(JSON.stringify(err), this.browserMenuEvents.shareFriends);
    });
    this.browser.on(this.browserMenuEvents.shareMoment).subscribe((res) => {
      this.shareWxTimeLine(res)
    }, (err) => {
      this.logger.log(JSON.stringify(err), this.browserMenuEvents.shareMoment);
    });
    this.browser.on(this.browserMenuEvents.Refresh).subscribe((res) => {
      this.browser.reload();
    }, (err) => {
      this.logger.log(JSON.stringify(err), this.browserMenuEvents.Refresh);
    });

    this.browser.on(this.browserMenuEvents.openInBrowser).subscribe((res) => { 
      let currenturl = Utility.removeSessionInfo(res.url || this.page.url);
      this.openInBrowser(this.page.appId, currenturl);
    })

    this.browser.on(this.browserMenuEvents.copyUrl).subscribe((res) => {
      let url = Utility.removeSessionInfoForShareLink(res.url)
      this.clipboard.copy(url);
    })

  }

  /**
   * 在本地浏览器打开
   * @param appId 
   * @param url 
   */
  openInBrowser(appId: any = 0, url: string) {
    this.getAuthCode(appId, url).subscribe((data: any) => {
    // this.httpService.post('v2/Auth/GetCode', true, { AppId: appId || 0 }).subscribe((data: any) => {
      if (data.errcode == 0) {
        var code = data.result.code;
        let completeUrl = Utility.completeUrlForBrowser(url, code, this.session, appId > '0' ? appId.toString() : null);
        this.openOnSystem(completeUrl)
      }
    });
  }

  InAppBrowserOptions(page: Webpage) {
    const isDarkMode = this.session.isDarkMode;
    this.options = {
      statusbar: {
        color: Utility.getThemeColor(isDarkMode).BG_COLOR,
        isDark: isDarkMode
      },
      toolbar: {
        height: 44,
        color: Utility.getThemeColor(isDarkMode).BG_COLOR
      },
      title: {
        color: Utility.getThemeColor(isDarkMode).TEXT_COLOR,
        showPageTitle: true,
        fontSize: 17  //安卓17，ios19
      },
      backButton: {
        wwwImage: Utility.getThemeColor(isDarkMode).BROWSER_IMG.BACKBUTTON.wwwImage,
        wwwImagePressed: Utility.getThemeColor(isDarkMode).BROWSER_IMG.BACKBUTTON.wwwImagePressed,
        wwwImageDensity: 2.2,
        align: 'left',
        event: 'backPressed'
      },
      closeButton: {
        wwwImage: Utility.getThemeColor(isDarkMode).BROWSER_IMG.CLOSEBUTTON.wwwImage,
        wwwImagePressed: Utility.getThemeColor(isDarkMode).BROWSER_IMG.CLOSEBUTTON.wwwImagePressed,
        align: 'right',
        event: 'closePressed',
        wwwImageDensity: 3.0,
      },

      browserProgress: {
        showProgress: true,
        progressBgColor: Utility.getThemeColor(isDarkMode).BORDER_COLOR,
        progressColor: "#1A83D4"
      },
      menu: {
        wwwImage: Utility.getThemeColor(isDarkMode).BROWSER_IMG.MENU.wwwImage,
        wwwImagePressed: Utility.getThemeColor(isDarkMode).BROWSER_IMG.MENU.wwwImagePressed,
        wwwImageDensity: 3.8,
        cancel: this.translate.instant('APP_COMMON_CANCEL'),
        align: 'right',
        items: [

        ]
      },
      backButtonCanClose: true
    };

    let NEWS_WIDGETID = '59907583691776';
    if (page.title && page.appId != NEWS_WIDGETID && page.appId) {
      this.options.title.staticText = page.title;
    }
  }

  getWidgetById(id): Widget {
    if (!id ) return null;

    if(this.session.widget){
      for (let item of this.session.widget) {
        for (let widget of item.widgets) {
          if (widget.id == id) {
            return widget
          }
        }
      }
    }

    if(this.session.homedata && this.session.homedata['widgets']){
      for (let widget of this.session.homedata['widgets']) {
        if (widget.id == id) {
          return widget
        }
      }
    }
    
    return null
  }
  clearBrowserCache() {
    let options = {
      hidden: true,
      clearcache: true
    }
    this.browser = this.themeableBrowser.create('about:blank', this.openBrowserTarget, options)
    this.browser.close();
    this.browser = null;
  }

  openFeedback(navCtrl) {
    let page: Webpage = {
      url: this.serverProvider.WebServer() + "WebApp/Faq/Index",  // 链接地址
      title: this.translate.instant('MY_LIST_HELP'),  // 标题
      description: "",   // 摘要内容
      imageUrl: "",   // 图片路径
      appId: "", // 應用ID,
      options: {
        canShare: false,
        allowCopyLink: false,
        allowOpenInBrowser: false,
      }
    };
    this.openWebpage(page, navCtrl);
  }
  getAllWidget(input: any, isHideLoading?: Boolean) {
    let seq = this.httpService.post('Widget/GetAll', true, input, isHideLoading).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }


  getAllForEdit(input: any) {
    let seq = this.httpService.post('Widget/GetAllForEdit', true, input).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  getFavorites() {
    let seq = this.httpService.post('Widget/Favorites', true, { "Lang": this.session.language }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  setFavorites(input: any) {
    let seq = this.httpService.post('Widget/SetFavorites', true, input).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }
  //轮播图
  getSlideList() {
    let seq = this.httpService.post('Content/SlideList', true, { "Lang": this.session.language }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }
  //友情链接
  getFriendlyLinkList() {
    let seq = this.httpService.post('Content/FriendlyLinkList', true, { "Lang": this.session.language }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }
  //友情链接
  getHomeData(isHideLoading?: Boolean) {
    let seq = this.httpService.post('Content/GetHomeData', true, { "Lang": this.session.language }, isHideLoading).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

  //搜素
  getSearch(searchInfo: any) {
    let seq = this.httpService.post('Content/Search', true, searchInfo, true).share();

    seq.subscribe((data: any) => {

    }, err => {
      this.logger.log(JSON.stringify(err), "Content/Search");
    });

    return seq;
  }


  getPageArticle(_SubjectId: number, _lang: string) {
    let seq = this.httpService.post('Content/PageArticle', false, {
      SubjectId: _SubjectId,
      lang: _lang
    }).share();
    seq.subscribe((data: any) => {
      // alert("save result:" + JSON.stringify(data));
      // this.nativeService.showToast("saveGeolocation completed");
    });
    return seq;
  }

  /**
   * 获取AuthCode，判断传入参数APPID是否为空，若为空则解析URL中APPID，作为接口参数
   * @param AppId 
   * @param URL 
   */
  getAuthCode(AppId, URL){ 
    if(!AppId)AppId = Utility.getQueryByName(URL,'appid') || "0"
    return this.httpService.post('v2/Auth/GetCode', true, { AppId: AppId}).share() 
  }
}



