import { TranslateService } from '@ngx-translate/core';
import { StorageService } from './storage-service';
import { Injectable } from '@angular/core';
import { Events } from 'ionic-angular';
import {
  Http, Response, Headers, RequestOptions, URLSearchParams, RequestOptionsArgs, RequestMethod
} from '@angular/http';
import { Observable, TimeoutError } from "rxjs"; 
import { Utility } from "./utility";
import { Session } from "./session";
import { NativeService } from "./native-service";
import { REQUEST_TIMEOUT, IS_DEBUG } from "./constants";
import { Logger } from "./logger";
import { HandleProvider } from './../providers/handle/handle';
 
import { ServerProvider } from './../providers/server-provider';

@Injectable()
export class HttpService {

  constructor(public http: Http,
    public Session: Session,
    public logger: Logger,
    public events: Events,
    public nativeService: NativeService,
    private serverProvider: ServerProvider,
    public storageService: StorageService,
    public translateService: TranslateService,
    public handleProvider: HandleProvider) {
  }

  count: number = 0;//记录未完成的请求数量,当请求数为0关闭loading,当不为0显示loading

  /**
   * 
   * @param url 请求URL， 默认使用API v1版本，若为其他版本，请加上前缀v** (eg: v2/Auth/Login)
   * @param needAuth 是否需要验证
   * @param options 请求option
   * @param isHideLoading 是否隐藏loading
   */
  public request(url: string, needAuth: boolean = false, options: RequestOptionsArgs, isHideLoading?: Boolean): Observable<Response> {

    // if (!this.nativeService.isConnecting()) {
    //   this.nativeService.alert('请连接网络');
    //   return;
    // }

    if(!isHideLoading){
      this.handleProvider.generate('request',0,url)
    }
    url = this.formatUrlDefaultApi(url);
    if (needAuth) {
      options = this.addAuthorizationHeader(options);
    }
    IS_DEBUG && console.log('%c 请求前 %c', 'color:blue', '', 'url', url, 'options', options);
    this.showLoading(isHideLoading);
    return Observable.create(observer => { 
      try{
        this.http.request(url, options).timeout(REQUEST_TIMEOUT).subscribe(res => {
          let data = this.requestSuccessHandle(url, options, res, false, isHideLoading);
          if (data.errcode == 0) {
            observer.next(data)
            observer.complete()
          } else if (data.errcode == 401 || (data.errcode >= 6000 && data.errcode < 6006)) {  //请求成功，返回值
            this.RefreshToken(url, options, observer, isHideLoading);
          } else {
            observer.error(data);
            observer.complete()
          }
        }, err => {
          observer.error(this.requestFailedHandle(url, options, err, isHideLoading));
          observer.complete()
        }, () => {
          // observer.complete()
        });
      }catch(e){
        observer.error(e);
      } 
    });
  }


  /**
   * token过期后重新获取token，并继续发出请求
   */
  RefreshToken(url, options, observer, isHideLoading?: Boolean) {
    this.showLoading(isHideLoading);
    const res = this.http.request(this.formatUrlDefaultApi('Auth/RefreshUserToken'), new RequestOptions({
      method: RequestMethod.Post,
      body: {
        "RefreshToken": this.Session.refreshToken,
        "OldToken": this.Session.token
      },
      headers: new Headers({
        'Content-Type': 'application/json; charset=UTF-8'
      })
    })).timeout(REQUEST_TIMEOUT);
    res.subscribe((res) => {
      let data = this.requestSuccessHandle(url, options, res, true, isHideLoading);

      if (data.result && data.result.refreshToken) this.Session.refreshToken = data.result.refreshToken;
      if (data.result && data.result.userToken) this.Session.token = data.result.userToken;
      if (data.result && data.result.userId) this.Session.userId = data.result.userId;

      const tokenObject = { userId: '', userToken: '', refreshToken: '' }
      tokenObject.userId = this.Session.userId;
      tokenObject.userToken = this.Session.token;
      tokenObject.refreshToken = this.Session.refreshToken;
      this.storageService.setItem('tokenObject', tokenObject);

      options = this.refreshAuthorizationHeader(options);   //重新对auth赋值
      this.http.request(url, options).timeout(REQUEST_TIMEOUT).subscribe((result) => {
        let data = this.requestSuccessHandle(url, options, result, true, isHideLoading);
        data.errcode == 0 ? observer.next(data) : observer.error(data)
        observer.complete()
      }, err => {
        observer.error(this.requestFailedHandle(url, options, err, isHideLoading));
        observer.complete()
      });
    })


  }

  /**
  * HTTP GET方法
  * @param url 请求URL， 默认使用API v1版本，若为其他版本，请加上前缀v** (eg: v2/Auth/Login)
  * @param needAuth 是否需要Token验证
  * @param paramMap 请求参数,eg : { account: 'test', password: '123'}
  * @param isHideLoading 是否隐藏loading,默认显示
  */
  public get(url: string, needAuth: boolean = false, paramMap: any = null, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Get,
      search: HttpService.buildURLSearchParams(paramMap)
    }), isHideLoading);
  }

  /**
  * HTTP POST方法
  * @param url 请求URL， 默认使用API v1版本，若为其他版本，请加上前缀v** (eg: v2/Auth/Login)
  * @param needAuth 是否需要Token验证
  * @param body 请求参数,eg : { account: 'test', password: '123'}
  * @param isHideLoading 是否隐藏loading,默认显示
  */
  public post(url: string, needAuth: boolean = false, body: any = {}, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Post,
      body: body,
      headers: new Headers({
        'Content-Type': 'application/json; charset=UTF-8'
      })
    }), isHideLoading);
  }

  public postFormData(url: string, needAuth: boolean = false, paramMap: any = null, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Post,
      body: HttpService.buildURLSearchParams(paramMap).toString(),
      headers: new Headers({
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      })
    }), isHideLoading);
  }

  public put(url: string, needAuth: boolean = false, body: any = {}, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Put,
      body: body
    }), isHideLoading);
  }

  public delete(url: string, needAuth: boolean = false, paramMap: any = null, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Delete,
      search: HttpService.buildURLSearchParams(paramMap).toString()
    }), isHideLoading);
  }

  public patch(url: string, needAuth: boolean = false, body: any = {}, isHideLoading?: Boolean): Observable<any> {
    return this.request(url, needAuth, new RequestOptions({
      method: RequestMethod.Patch,
      body: body
    }), isHideLoading);
  }


  /**
   * 处理请求成功事件
   */
  requestSuccessHandle(url: string, options: RequestOptionsArgs, res: Response, isCheck: Boolean, isHideLoading?: Boolean) {
    this.hideLoading(isHideLoading); 
    let json = res.json();
    if (url.indexOf(this.serverProvider.BaseApiServer()) != -1) {
      if (json.errcode != 0) {
        IS_DEBUG && console.log('%c 请求失败 %c', 'color:red', '', 'url', url, 'options', options, 'err', res);
        if (json.errcode == 401 || (json.errcode >= 6000 && json.errcode <= 6010)) {//401 token无效或过期需要重新登录
          //this.nativeService.showToast('用户会话已过期,请重新登录'); 
          if (isCheck) {
            this.events.publish('user:reLogin', Date.now());
          }
        } else {
          //this.nativeService.alert(json.errmsg || '请求失败,请稍后再试!');
        }
        return json;
      } else {
        IS_DEBUG && console.log('%c 请求成功 %c', 'color:green', '', 'url', url, 'options', options, 'res', res);
        return json;//{success: true, data: json};
      }
    } else {
      return { errcode: 0, result: json };
    }
  }


  /**
   * 处理请求失败事件
   */
  private requestFailedHandle(url: string, options: RequestOptionsArgs, err: Response, isHideLoading?: Boolean) {
    IS_DEBUG && console.log('%c 请求失败 %c', 'color:red', '', 'url', url, 'options', options, 'err', err);
    this.hideLoading(isHideLoading);
    if (!this.nativeService.isConnecting()) {
      this.nativeService.alert(this.translateService.instant('HTTP_ERROR_STATUS_DISCONNECTED'));
    } else if (err instanceof TimeoutError) {
      // this.nativeService.alert(this.translateService.instant('HTTP_ERROR_STATUS_TIMEOUT'));
      this.handleProvider.generate('request timeout',-1, {
        url:url,
        error: err
      })
    } else {
      let status = err.status;
      let msg = this.translateService.instant('HTTP_ERROR_STATUS_UNKNOWN');
      if (status === 0) {
      } else if (status === 404) {
        msg = this.translateService.instant('HTTP_ERROR_STATUS_404');
        // this.nativeService.alert(msg);
      } else if (status === 500) {
        msg = this.translateService.instant('HTTP_ERROR_STATUS_500');
        // this.nativeService.alert(msg);
      } else {
        // this.nativeService.alert(msg);
      }

      this.logger.log(JSON.stringify(err), msg + url)
      this.logger.httpLog(err, msg, {
        url: url,
        status: status
      });
    }
    return err;
  }

  /**
   * 将对象转为查询参数
   */
  private static buildURLSearchParams(paramMap): URLSearchParams {
    let params = new URLSearchParams();
    if (!paramMap) {
      return params;
    }
    for (let key in paramMap) {
      let val = paramMap[key];
      if (val instanceof Date) {
        val = Utility.dateFormat(val, 'yyyy-MM-dd hh:mm:ss')
      }
      params.set(key, val);
    }
    return params;
  }

  /**
   * 格式化url使用默认API地址
   */
  private formatUrlDefaultApi(url: string = ''): string {

    let resUrl = Utility.formatUrl(url.startsWith('http') ? url : this.serverProvider.ApiServer() + url)
    /** 默认使用API v1版本，若为其他版本，则替换掉v1 */
    if (resUrl) {
      const reg = /\/frontapi\/v1\/v([0-9]*)/
      resUrl = resUrl.replace(reg, '/frontapi/v$1')

      // const reg1 = /42151\/v1\/v([0-9]*)/
      // resUrl = resUrl.replace(reg1, '42151/v$1')
    }
    return resUrl
  }

  /**
   * 给请求头添加权限认证token
   */
  private addAuthorizationHeader(options: RequestOptionsArgs): RequestOptionsArgs {
     let token = this.Session.token;

    //let token ='eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJpUHJvdWQiLCJhdWQiOiI1MDI1ODc2MjU0MzEwNCIsImV4cCI6IjE1NjQ3OTU5NjQiLCJuYmYiOiI4LzMvMjAxOSAxMjozMjo0NCBBTSIsImp0aSI6IjExMSJ9.pLM-xdQrJYhzKbROgEbetq0wQLymt1T1y5MSFf514xo'
    if (token != null && token != undefined) {
      if (options.headers) {

        options.headers.append('Authorization', 'Bearer ' + token);
      } else {
        options.headers = new Headers({
          'Authorization': 'Bearer ' + token
        });
      }

    }
    // return this.addSignatureIntoHeaders(options);
    return options;
  }

  private refreshAuthorizationHeader(options: RequestOptionsArgs): RequestOptionsArgs {
    let token = this.Session.token;
    if (token != null && token != undefined) {
      if (options.headers) {
        options.headers.delete('Authorization');
        options.headers.append('Authorization', 'Bearer ' + token);
      } else {
        options.headers = new Headers({
          'Authorization': 'Bearer ' + token
        });
      }
    }
    // return this.addSignatureIntoHeaders(options);
    return options;
  }

  addSignatureIntoHeaders(options: RequestOptionsArgs){ 
    if(!options.headers.has('Signature')){
      let signature = Utility.generateSignature() 
      options.headers.append('Signature', signature['Signature']); 
      options.headers.append('Timestamp', signature['Timestamp']);
      options.headers.append('Nonce', signature['Nonce']);
    }
    return options;
  }

  private showLoading(isHideLoading?) {
    if (!isHideLoading) {
      if (++this.count > 0) {//一旦有请求就弹出loading
        this.Session.showLoading && this.nativeService.showLoading();
      }
    }
  }

  private hideLoading(isHideLoading?) {
    if (!isHideLoading) {
      if (this.Session.showLoading) {
        //延迟处理可以避免嵌套请求关闭了第一个loading,突然后弹出第二个loading情况(结合nativeService.showLoading())
        setTimeout(() => {
          if (--this.count === 0) {//当正在请求数为0,关闭loading
            this.nativeService.hideLoading();
          }
        }, 200);
      } else {
        this.Session.showLoading = true;
      }
    }

  }
}


