import { SetPasswordPage } from './../pages/my/set-password/set-password';
import { DEEPLINKS_TYPE, WEIXIN_APPID } from './../services/constants';
import { Deeplinks } from '@ionic-native/deeplinks';

import { HandleProvider } from './../providers/handle/handle';
import { Device } from '@ionic-native/device';

import { workflow_tabs, MessageListPage, MessageChatPage } from './../pages/pages';

import { Utility } from './../services/utility';
import { Location } from '@angular/common';

import { Component, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicApp, Config, Nav, Platform, ModalController, Events, App, AlertController } from 'ionic-angular';
import { Globalization } from '@ionic-native/globalization';
import { MainPage, Tab1Root, Tab2Root } from '../pages/pages';
import { LoginPage, BrowserPage, UpgradePage, TutorialPage } from '../pages/pages';
import { ConfigProvider, AuthProvider, MessageProvider, UserProvider, SettingProvider, CacheProvider, ServerProvider, WidgetProvider } from '../providers/providers';
import { Session, NativeService, StorageService, JPushService, Logger } from '../services/services';
import { Message, Webpage } from '../models/models';
import moment from 'moment';
import { TUTORIAL_FLAG_NAME } from '../services/constants';
import { SqliteProvider } from '../providers/sqlite-provider'; 
export interface IPROUDLOG {
  handleTime: string,
  result: any;
  name: string
}
@Component({
  template: `
  <ion-nav #content [root]="rootPage"></ion-nav>`
})
export class MyApp {
  isDark: boolean = false; //是否为黑夜模式
  rootPage = ''; //首页
  backButtonPressed: boolean = false;  //用于判断返回键是否触发 
  isLoadInit: boolean = false; //是否已经初始化 
  count: number = 0; //重新登录计数器 
  isShowLoginPage: boolean = false;
  selectOnMessageCount: 0;
  logArray: any[]; 
  isLogin: boolean = false;
  @ViewChild(Nav) nav: Nav;

  constructor(public ionicApp: IonicApp,
    private translate: TranslateService,
    private platform: Platform,
    private config: Config,
    private nativeService: NativeService,
    private storageService: StorageService,
    private authProvider: AuthProvider,
    private events: Events,
    private jpushService: JPushService,
    private messageProvider: MessageProvider,
    private userProvider: UserProvider,
    private session: Session,
    private configProvider: ConfigProvider,
    private modalCtrl: ModalController,
    private globalization: Globalization,
    public alertCtrl: AlertController,
    public app: App,
    private serverProvider: ServerProvider,
    private setting: SettingProvider,
    private location: Location,
    private cacheProvider: CacheProvider,
    public logger: Logger,
    public device: Device,
    public handleProvider: HandleProvider,
    public sqlprovider: SqliteProvider ,
    private deeplinks: Deeplinks,
    private widgetProvider: WidgetProvider
  ) {
   
    
    this.logArray = [];
    // this.generate('app.component.constructor', 'constructor is crated'); 
    platform.ready().then((readySource) => {
      // Utility.test()
      // Utility.testRemoveSessionInfo() 
      if(this.nativeService.isMobile()   ){
        let routeObj = {}
        routeObj[`/${DEEPLINKS_TYPE.OPENURL}`] = DEEPLINKS_TYPE.OPENURL;
        routeObj[`/${DEEPLINKS_TYPE.OPENPAGE}`] = DEEPLINKS_TYPE.OPENPAGE;
        routeObj[`/${WEIXIN_APPID}`] = WEIXIN_APPID;
        
        this.deeplinks.route(routeObj).subscribe(match => {
          // match.$route - the route we matched, which is the matched entry from the arguments to route()
          // match.$args - the args passed in the link
          // match.$link - the full link data
          this.handleProvider.generate('deeplink match',-1, match);
          let route = match.$route;
          let args = match.$args;
          // let link = match.$link;
          if(WEIXIN_APPID == route && this.nativeService.isIos()){
            this.events.publish('wechat-auth', args)
          }else{
            this._openBrowserFromWebLink(args,route);
          } 
        }, nomatch => {
          let link = nomatch.$link; 
          if(WEIXIN_APPID == link.scheme && this.nativeService.isIos()){
            this.events.publish('wechat-auth', Utility.parseQuery(link.url))
          }else{
            this.handleProvider.generate('deeplink no match',-1, nomatch);
          }  
        });
      }
      
      this.handleProvider.init() 
      this.handleProvider.generate('platform ready success',0, readySource);
      // this.nativeService.writeLog(JSON.stringify(this.logArray, null, 2)) 
      this.initNative();
      this.setting.load();//初始化设置 
      this.init(); //是否需要导览 

    }).catch(error => { 
      this.handleProvider.generate('platform ready failed',-1, error);
    });
    document.addEventListener("resume", () => {
      this.resume();
    }, false);

    document.addEventListener("pause", () => {
      this.pause();
    }, false);
    this.initTranslate();
  }

  _openBrowserFromWebLink(args:any, route: string){ 
    this.handleProvider.generate('_openBrowserFromWebLink',-1, this.session.userId)
    if(this.session.userId){
      switch(route){
        case DEEPLINKS_TYPE.OPENURL: 
          let widget = this.widgetProvider.getWidgetById(args.appId); 
          let page: Webpage = {
          url: decodeURIComponent(args.redirectUrl)  ,
          title: args.title ? decodeURIComponent(args.title) : '', 
          imageUrl: args.imageUrl ? decodeURIComponent(args.imageUrl) : '', 
          description: args.description ? decodeURIComponent(args.description) : '', 
          appId: args.appId, 
          options: {
            canShare: widget ? widget.canShare : args.canShare ,
            allowCopyLink: widget ? widget.allowCopyLink : true ,
            allowOpenInBrowser: widget ? widget.allowOpenInBrowser : true,
            useNativeBrowser: widget ? widget.useNativeBrowser : '' 
          }
        } 
        this.widgetProvider.openWebpage(page,this.nav, Utility.boolean(args.isAddAccountId));
          break;
        case DEEPLINKS_TYPE.OPENPAGE:
          this.nav.push(route,args)
          break;  
        default: 
      } 
    }else{
      this.events.unsubscribe('openWidget:logined');
      this.events.subscribe('openWidget:logined',()=>{
        this.handleProvider.generate('openWidget:logined',-1, {args: args, route: route})
        this._openBrowserFromWebLink(args,route)
      }) 
    } 
  }


  /**
   * app进入前台事件
   */
  resume() {
    this.initNative()
    this.handleProvider.generate('platform:resume',0, `isLoadInit:${this.isLoadInit}`); 
  }


  /**
   * app进入后台事件
   */
  pause() {
    // alert("qidong")
    this.handleProvider.generate('platform:pause',0);
  }

  generate(name: string, result: any) {
    let date = new Date()
    let time = date.getTime();
    let item: IPROUDLOG = {
      name: name,
      result: result,
      handleTime: Utility.dateFormat(new Date(), 'yyyy-MM-dd HH:mm:ss') + ' ' + time.toString().substr(time.toString().length - 3, 3)
    }
    
    this.logArray.push(item) 
  }
  async init() {
    // if (this.isLoadInit) return;//判断是否已经初始化，若未初始化则进行初始化
    this.handleProvider.generate('component:init',0);
    let isHideTutorial = null;
    try {
      /**是否需要导览 */
      isHideTutorial = await this.storageService.getItem(TUTORIAL_FLAG_NAME)
      this.isDark = await this.storageService.getItem('optionTheme')
    } catch (e) {
      isHideTutorial = false;
      this.isDark = false;
    } finally {
      this.setThemeMode(this.isDark)
      /**
       * 隐藏导览
       */
      if (Utility.boolean(isHideTutorial)) {
        this.rootPage = MainPage;

        // this.init(); 
        this.initUserInfo();//初始化个人信息
        this.configProvider.init();//初始化版本信息
      } else {
        this.rootPage = TutorialPage;
      }

      /**跳过导览后初始化app */
      this.events.subscribe('tutorial:init', () => {
        this.initUserInfo();//初始化个人信息
        this.configProvider.init();//初始化版本信息
        this.nativeService.statusBarStyle(this.isDark);
      });
    }


    this.isLoadInit = true;
    this.jPushOpenNotification();//先訂閱消息事件，再初始化極光推送 
    this.cacheProvider.init();//初始化缓存信息
   

    this.initGlobalization();// 取得设备端全球化设置，日期格式，时区等
    this.registerBackButtonAction(); //註冊返回按鍵
    this.initBrowserBackButton();//订阅browser返回按钮事件
    //this.initProductionListener(); //订阅是否为生产环境
    this.initListeningUserIsActive();//订阅用户是否活动状态
    this.initVersionListener(); //订阅有关版本事件
    this.initGlobalSetting();//获取全局设置
    // this.configProvider.ConfigDeeplinks(this.nav);
    // 订阅重新登录事件
    this.events.subscribe('user:reLogin', () => {
      this.isLogin = false;
      //用于判断登录页面是否已经显示 
      this.openModal(this.openLoginModal)
    });
    /** 主题变更事件 */
    this.events.subscribe('setting:change-theme', isDark => {
      this.setThemeMode(isDark)
    })

    /**
     * 登录成功后关闭所有modal
     */
    this.events.subscribe('closemodal:login-success', () => {
      this.closeAllModalPage();
    })

    /**获取键盘高度，并存在session里面 */
    window.addEventListener('native.keyboardshow', (e: any) => {
      this.session.keyboardHeight = e.keyboardHeight;
    });

    // this.handleProvider.generate('events subscribe user:logined',-1,0)
    this.events.subscribe('user:logined', ()=>{ 
      if(this.isLogin)return;
      this.widgetProvider.getConfig()
      this.isLogin = true;
      this.events.publish('openWidget:logined'); //通知继续打开应用
      // this.handleProvider.generate('user:logined',-1,this.session)
      this.configProvider.initDatabase();
      
    })
  }
  initTranslate() {
    this.translate.addLangs(['en-US', 'zh-TW', 'zh-CN', 'vi-VN']);
    this.translate.setDefaultLang('en-US');
    if (this.session.language) {
      this.storageService.setItem("language", this.session.language);
      this.translate.use(this.session.language);
    } else {
      this.storageService.getItem("language").then((lang) => {
        if (lang) {
          this.session.language = lang;
        } else {
          this.session.language = 'en-US';
          let browserLang = this.translate.getBrowserLang();

          if (browserLang) {
            if (this.translate.getLangs().indexOf(browserLang) >= 0) {
              this.session.language = browserLang;
            } else if (browserLang === 'zh') {
              const browserCultureLang = this.translate.getBrowserCultureLang();

              if (browserCultureLang.match(/-CN|CHS|Hans/i)) {
                this.session.language = 'zh-CN';
              }
              else {
                this.session.language = 'zh-TW';
              }
            }
          }
        }
        this.storageService.setItem("language", this.session.language);
        this.translate.use(this.session.language);
      });
    }
  }

  /**
   *  进入导览之后设置状态栏颜色
   * @param value 是否为黑夜模式
   * @param isTutorial 是否进入导览
   */
  setThemeMode(value) {
    this.session.isDarkMode = Utility.boolean(value);
    this.nativeService.statusBarStyle(Utility.boolean(value));
    let attr = document.createAttribute('class');
    attr.value = Utility.getThemeName(Utility.boolean(value));
    document.body.attributes.setNamedItem(attr);

    /** 安卓添加andorid-header到body，android-header 在theme/variables.scss 中定义 */
    if (this.nativeService.isAndroid()) {
      document.body.classList.add("android-header")
    }

  }

  //检测网络
  assertNetwork() {
    if (!this.nativeService.isConnecting()) {
      this.translate.stream("APP_NETWORK_DISCONNECT").subscribe((text) => {
        this.nativeService.showToast(text);
      })
    }
  }

  // 加載用戶信息，如果用戶登錄過，則直接跳轉到首頁，否則進入歡迎頁面
  async initUserInfo() {
    try {
      const tokenObject = await this.storageService.getItem('tokenObject');//从缓存中获取token
      const language = await this.storageService.getItem('language')
      const homedata = await this.storageService.getItem('homedata')
      if (language) this.session.language = language;
      if (tokenObject) {
        const data = {
          result: { userId: '', userToken: '', refreshToken: '' }
        }
        if (!tokenObject.userId || !tokenObject.refreshToken) { // 
          this.openModal(this.openLoginModal)
          return;
        }
        data.result.userId = tokenObject.userId;
        data.result.userToken = tokenObject.userToken;
        data.result.refreshToken = tokenObject.refreshToken;

        let idshowloading: boolean = false;
        if (homedata) idshowloading = true;
        this.handleProvider.generate('initUserInfo',0, data);
        this.authProvider.loginSuccessHandle(data, idshowloading);
      } else {
        this.openModal(this.openLoginModal)
      }
    } catch (err) {
      this.openModal(this.openLoginModal)
      this.handleProvider.generate('initUserInfo',-1, err);
      this.logger.log(JSON.stringify(err), 'this.storageService.getItem(\'tokenObject\')')
    }


  }

  //处理安卓硬件返回按钮 
  registerBackButtonAction() { 
    this.events.subscribe('backbutton:onclick',()=>{
      this._viewDismiss()
    })
    this.platform.registerBackButtonAction(() => {
       this._viewDismiss() 
    }, 1);
  }

  /**
   * 返回上一页
   */
  _viewDismiss(){
    let activePortal = this.ionicApp._modalPortal.getActive() || this.ionicApp._loadingPortal.getActive();
    if (activePortal) {
      let instance = activePortal.instance;
      if (instance.constructor.name == LoginPage || instance.constructor.name == SetPasswordPage) {
        //当前页面为登录页面，退出
        return this.showExit();
      }else {
        let viewCtrl = instance.view || instance.viewCtrl 
        viewCtrl.dismiss()
        return;
      }
    }

    const overlay = this.app._appRoot._overlayPortal.getActive();
    const nav = this.app.getActiveNavs()[0];
    const currentId = nav.getActive().id;

    if (workflow_tabs.indexOf(currentId) > -1) {
      this.events.publish('back-to-home');
    } else if (currentId == BrowserPage) {
      window.history.back();
      setTimeout(() => { this.events.publish('back_registration') }, 200);
    } else if (overlay && overlay.dismiss) {
      overlay.dismiss();
    } else if (nav.canGoBack()) {
      nav.pop();
    } else {
      return this.showExit();
    }
  }

  //双击退出提示框
  showExit() {
    if (this.backButtonPressed) { //当触发标志为true时，即2秒内双击返回按键则退出APP
      this.platform.exitApp();
    } else {
      this.nativeService.showToast("再按一次退出应用", 2000, "top");
      this.backButtonPressed = true;
      setTimeout(() => this.backButtonPressed = false, 2000);//2秒内没有再次点击返回则将触发标志标记为false
    }
  }

  /**
   * 收到信息事件訂閱
   */
  jPushOpenNotification() {
    // 当收到推送消息时（此时将消息存储）
    this.events.subscribe('iProud.receiveNotification', extras => {
      extras.widgetId = extras.widgetId || 0;
      let message: Message = {
        id: extras.messageId,
        widgetId: extras.widgetId,
        widgetName: extras.widgetName,
        widgetIcon: extras.widgetIcon,
        title: extras.title,
        abstract: extras.abstract,
        formatType: extras.formatType,
        imageUrl: extras.imageUrl,
        redirectUrl: extras.redirectUrl,
        receiveTime: moment().utc().format(),
        isRead: false,
        status: 'success',
        extra: {
          groudId: extras.groudId , //群组id
          senderId: extras.senderId, //发送人id
          senderName: extras.senderName ,//发送人名称
          senderAvator : extras.senderAvator ,//发送人头像
          fromType: extras.fromType ,//发送来源类型
        }
      };

      if ([MessageListPage, MessageChatPage].indexOf(this.getCurrentPageId()) > -1 
      && (message.widgetId == this.session.messageWidgetId || extras.senderId == this.session.messageWidgetId )) {
        message.isRead = true;
      }

      this.handleProvider.generate("component:iProud.receiveNotification",0,extras)
      this.messageProvider.addMessage(message)
       setTimeout(() => { this.events.publish('jpush.notificationReceived', message) }, 100) 
    });

    // 收到自定义消息处理
    this.events.subscribe('jpush.receiveCustomizeMessage', extras => {
      if (extras && extras.event) {
        switch (extras.event) {
          case 'version-upgrade': this.configProvider.init();
            break;
          case 'lock-user': this.events.publish('user-isActive:false')
            break;
          case 'execute-javascript':
            break;
          default:
        }
      }
    })

    // 当点击极光推送消息跳转到指定页面
    this.events.subscribe('iProud.openNotification', content => {
      this.handleProvider.generate('component:openNotification',0, content);
      this.selectdOnMessage();
      setTimeout(()=>{
        this.events.publish('iProud.receiveNotification', content);
      },100)
      
    });
  }

  selectdOnMessage() {
    try {
      const tabs = this.nav.getActiveChildNav();
      const tab = tabs.getSelected();
      const activeVC = tab.getActive();
      if (activeVC.component == Tab2Root) {//如果当前所在页面就是将要跳转到的页面则不处理
        return;
      }
      const activeNav = activeVC.getNav();
      activeNav.popToRoot({}).then(() => {// 导航跳到最顶层
        tabs.select(1); // 选中第二个tab 
      });
    } catch (e) {
      if (this.selectOnMessageCount < 3) {
        setTimeout(() => {
          this.selectdOnMessage();
        }, 1000)
      }
      this.selectOnMessageCount++;
    }
  }
  // 取得设备端全球化设置，日期格式，时区等
  initGlobalization() {
    this.globalization.getDatePattern({ formatLength: 'short', selector: 'date' }).then((result) => {
      this.session.shortDatePattern = result.pattern,
        this.session.timezone = result.timezone,
        this.session.utcOffset = result.utc_offset,
        this.session.dstOffset = result.dst_offset
    });
    this.globalization.getDatePattern({ formatLength: 'full', selector: 'date' }).then((result) => {
      this.session.longDatePattern = result.pattern
    });

    // 当收到推送消息时（此时将消息存储）
    this.events.subscribe('user:logined', () => {
       
      this.count = 0; //登录成功后清空重新登录次数
      this.isShowLoginPage = false;
      //更新用户全球化设置
      let setting = new Array<any>();
      if (this.session.language != null) {
        setting.push({ "name": "Lang", "value": this.session.language });
      }
      if (this.session.shortDatePattern != null) {
        setting.push({ "name": "ShortDatePattern", "value": this.session.shortDatePattern });
      }
      if (this.session.shortDatePattern != null) {
        setting.push({ "name": "LongDatePattern", "value": this.session.longDatePattern });
      }
      if (this.session.shortDatePattern != null) {
        setting.push({ "name": "Timezone", "value": this.session.timezone });
      }
      if (this.session.shortDatePattern != null) {
        setting.push({ "name": "UtcOffset", "value": this.session.utcOffset });
      }
      if (this.session.shortDatePattern != null) {
        setting.push({ "name": "DstOffset", "value": this.session.dstOffset });
      }
      if (this.session.userId != null) {
        setting.push({ "name": "userAgent", "value": navigator.userAgent });
        setting.push({ "name": "appVersion", "value": this.configProvider.currentVersionNo });
        setting.push({ "name": "appTheme", "value": Utility.getThemeName(this.session.isDarkMode) });

        if (this.device && this.device.platform) {
          setting.push({ "name": "isVirtual", "value": this.device.isVirtual });
          setting.push({ "name": "manufacturer", "value": this.device.manufacturer });
          setting.push({ "name": "platform", "value": this.device.platform });
          setting.push({ "name": "model", "value": this.device.model });
          setting.push({ "name": "serial", "value": this.device.serial });
          setting.push({ "name": "uuid", "value": this.device.uuid });
          setting.push({ "name": "osversion", "value": this.device.version });
        } 
      }

      //先从服务器获取设置进行对比，值不同时在保存，减少API调用
      this.userProvider.getPrivateSetting().subscribe(res => {
        let serverSetting = {}; 
        if(!res['result'])return
        for (let item of res['result']) { 
          serverSetting[item.name.toUpperCase()] = item.value;
        }
        
        //服务器设置中，已设置ReceiveMsgFromWF的值并且不为false，默认保存为ReceiveMsgFromWF为 true
        if(serverSetting['ReceiveMsgFromWF'.toUpperCase()]!== 'false' && serverSetting['ReceiveMsgFromWF'.toUpperCase()] !== false ){
          setting.push({ "name": "ReceiveMsgFromWF", "value": true });
        }
        this.session.setting = serverSetting  
        let saveSetting = new Array<any>(); 
        for(let item of setting){ 
          if(serverSetting[item.name.toUpperCase()] != item.value){
            saveSetting.push(item)
          } 
        }

        if(saveSetting.length > 0){ 
          setTimeout(()=>{ 
            
            this.userProvider.privateSetting(saveSetting);
          },200)
        }
      }, err => {  
        this.userProvider.privateSetting(setting);
      }); 

      

      //登录后再获取地理位置，避免在token无效的情况下保存失败。
      this.initGeolocation();
    });
  }

  /**获取用户坐标 */
  initGeolocation() {
    this.nativeService.getCurrentPosition().subscribe((location) => {
      this.userProvider.saveGeolocation(location);
    }, (err) => {
      this.logger.log(JSON.stringify(err), 'this.nativeService.getCurrentPosition()')
    })
  }

  /**浏览界面返回按钮处理 */
  initBrowserBackButton() {
    this.events.subscribe('back_registration', () => {
      //接收延时的通知事件，判断是否在browser页面，若不是则手动返回上一页
      if (this.location.path().split('/').pop() != 'browser') {
        this.app.getActiveNavs()[0].pop();
      }
    })
  }

  /**切换版本 */
  initProductionListener() {
    this.events.subscribe('release-version-invalid', () => {
      if (!this.serverProvider.isDebug) {
        let confirm = this.alertCtrl.create({
          title: this.translate.instant('VERSION_EXCHANGE_TIPS'),
          message: this.translate.instant('VERSION_EXCHANGE_MESSAGE'),
          buttons: [
            {
              text: this.translate.instant('APP_COMMON_CANCEL'),
              handler: () => {
              }
            },
            {
              text: this.translate.instant('APP_COMMON_CONFIRM'),
              handler: () => {
                this.serverProvider.isDebug = true;
                this.storageService.setItem("isdebug", true);
                this.setting.setValue("optionDebug", true);
                this.isLoadInit = false;
                this.init();
              }
            }
          ]
        });
        confirm.present();
      }

    });
  }

  /**用户是否被禁用 */
  initListeningUserIsActive() {
    this.events.subscribe('user-isActive:false', () => {
      let alert = this.alertCtrl.create({
        title: this.translate.instant('ACCOUNT_LOCKED'),
        buttons: [{
          text: this.translate.instant('APP_COMMON_CONFIRM'),
          handler: () => {
            this.openLoginModal();
          }
        }]
      });
      alert.present();
    })
  }

  /**检测是否有版本有更新 */
  initVersionListener() {

    /** 获取到全局设置，或接收到有更新提示 */
    this.events.subscribe('version:upgrade-available', () => {
      if (this.configProvider.canUpgrade && this.session.globalSetting) {
        this.openModal(this.openUpgradePageModal)
      }
    });


    /**忽略更新后重新初始化 */
    this.events.subscribe('upgrade:re-init', () => {
      if (!this.isShowLoginPage && this.session.userId) {
        this.configProvider.isShowUpgraderPage = false;
        this.count = 0;
        this.init();
      }
    });
  }

  /**打开登录界面 */
  openLoginModal(_that?) {
    _that = _that || this
    let isShowLoginModal = false;
    if (_that.getActivityModalId() == LoginPage || _that.getActivityModalId() == UpgradePage) {
      isShowLoginModal = true;
    }
    if (!isShowLoginModal && !_that.isShowLoginPage) {

      _that.isShowLoginPage = true;
      _that.modalCtrl.create(LoginPage).present();
    }
  }

  /**打开升级界面 */
  openUpgradePageModal(_that?) {
    _that = _that || this
    _that.configProvider.isShowUpgraderPage = true;
    let isUpgradeShow = false;
    if (_that.getActivityModalId() == UpgradePage) {
      isUpgradeShow = true;
    }
    if (!isUpgradeShow) {
      _that.modalCtrl.create(UpgradePage, null, { showBackdrop: false, enableBackdropDismiss: false }).present();
    }
  }

  openModal(func: Function) {
    if (!this.configProvider.isShowUpgraderPage) {
      setTimeout(() => {
        func(this)
      }, 200 * this.count)
      this.count++;
    }

  }

  /**
   * 获取当前活动页面
   */
  getCurrentPageId() {
    try{
      const nav = this.app.getActiveNavs()[0];
      const currentId = nav.getActive().id;
      return currentId;
    }catch(e){
      this.handleProvider.generateError('getCurrentPageId',e)
      return ''
    } 
  }


  /**
   * 获取当前活动modal
   */
  getActivityModalId() {
    let activePortal = this.ionicApp._modalPortal.getActive();
    if (activePortal) {
      let instance = activePortal.instance;
      // this.nativeService.showToast(instance.constructor.name)
      return instance.constructor.name
    }
    return "";
  }

  initGlobalSetting() {
    this.configProvider.getGlobalSettings().subscribe(res => {
      this.session.globalSetting = res.result;
      this.events.publish('version:upgrade-available');
    })

    this.configProvider.getLangContext().subscribe(res => {  
    })
  }

  async initTutorial() {

  }

  /**
   * 初始化native相关
   */
  initNative() { 
    
    // 设置userAgent, 加上员宝标识 app/iproud，供网页判断运行环境是否在员宝内
    this.nativeService.getUserAgent().subscribe(userAgent =>{
      this.handleProvider.generateInfo("getUserAgent",userAgent)

      if(userAgent && userAgent.indexOf('app/iproud') == -1){
        this.nativeService.setUserAgent(userAgent + ' app/iproud').subscribe(res => {
          this.handleProvider.generateInfo("setUserAgent",res)
        },error => this.handleProvider.generateError("setUserAgent error",error))
      } 
    },err => {
      this.handleProvider.generateError("getUserAgent error",err)
    })

    setTimeout(() => {
      this.nativeService.splashScreenHide();
      setTimeout(() => {
        this.events.publish('homemain:resize-content')
      }, 100)
    }, 500)
    
    this.nativeService.hideKeyboardAccessoryBar(false);

    //網絡連接時
    this.nativeService.networkOnConnect().subscribe((value) => {
      this.nativeService.showLoading();
      setTimeout(() => {
        this.nativeService.hideLoading();
        if (this.getCurrentPageId() == Tab1Root) {
          this.init();
        } 
      }, 1000)
    }, (error) => {
      this.logger.log(JSON.stringify(error), 'this.nativeService.networkOnConnect()')
    })

    //斷開網絡時
    this.nativeService.networkOnDisconnect().subscribe((value) => {
      this.nativeService.showToast(this.translate.instant('APP_NETWORK_ONDISCONNECTED'));
    }, (error) => {
      this.logger.log(JSON.stringify(error), 'this.nativeService.networkOnDisconnect()')
    })
  }

  /**
   * 关闭所有modal
   */
  closeAllModalPage() {
    let views = this.ionicApp._modalPortal._views;
    if (views && views.length > 0) {
      for (let item of views) {
        item.dismiss()
      }
    }
  }
}



