import { DLApplyInfo } from './../models/DLApplyInfo';
import { StorageService } from './../services/storage-service';
import { NativeService } from './../services/native-service';
import { Events } from 'ionic-angular';

import { Injectable } from '@angular/core'; 
import { HttpService } from '../services/http-service';
import { Session } from '../services/session';


@Injectable()
export class DLRecruitingProvider {

  constructor(private httpService: HttpService,
    private session: Session,
    public events: Events,
    public nativeService: NativeService,
    public storageService: StorageService,) { 
    }
    GetSiteList() {
      let seq = this.httpService.post('DLRecruiting/GetSiteList', true,{ "lang": this.session.language  }).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    } 
    GetJobList(SiteCode?) {
      let seq = this.httpService.post('DLRecruiting/GetJobList', true, { "lang": this.session.language  }).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    }

    GetJobDescription(SiteCode: string) {
      let seq = this.httpService.post('DLRecruiting/GetJobDescription', true, { "lang": this.session.language , "SiteCode": SiteCode}).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    }
    
  
    
    GetPostAndInterviewDate(SiteCode: string) {
      let seq = this.httpService.post('DLRecruiting/GetPostAndInterviewDate', true, { "lang": this.session.language , "SiteCode": SiteCode}).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    }
 
    
    GetFAQInfo(SiteCode: string) {
      let seq = this.httpService.post('DLRecruiting/GetFAQInfo', true, { "lang": this.session.language , "SiteCode": SiteCode}).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    }
    
 

    SaveApplyJob(ApplyJobInput: DLApplyInfo) {
      let seq = this.httpService.post('DLRecruiting/SaveApplyJob', true, ApplyJobInput ).share();
      seq.subscribe((data: any) => {
  
      });
      return seq;
    }
    
    
}



