import { StorageService } from './../services/storage-service';
import { NativeService } from './../services/native-service';
import { Events } from 'ionic-angular';

import { Injectable } from '@angular/core';

import { HttpService } from '../services/http-service';
import { Session } from '../services/session';


@Injectable()
export class ContentProvider {

  constructor(private httpService: HttpService,
    private session: Session,
    public events: Events,
    public nativeService: NativeService,
    public storageService: StorageService,) {

    }
 
  getParkLifeList() {
    let seq = this.httpService.post('Content/Parklife', true, { "lang": this.session.language }).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  } 
  getNewsList(subjectId:number,skip?:number,take?:number,isHideLoading?: boolean) { 

    let subject = {
      "lang": this.session.language , 
      "skip": skip || 0,
      "take": take || 10
    }

    if(subjectId){
      subject['SubjectId'] = subjectId
    }
 
    
    let seq = this.httpService.post('Content/News', true, subject, isHideLoading).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  }

   

  getNewsSubjects() { 
    let seq = this.httpService.post('Content/NewsSubjects', true,{Lang:this.session.language}).share();
    seq.subscribe((data: any) => {

    });
    return seq;
  } 

  
}


